import React, { useState, useEffect, Fragment } from "react";
import { useDispatch, useSelector } from "react-redux";
import { useNavigate,useParams } from "react-router-dom";
import { toast } from "react-toastify";

import "../Admin/UpdateProfile.css";
import Loader from "../layout/Loader/Loader";
import InventorySidebar from "../layout/Loader/InventorySidebar";
import {clearErrors, updateItem,getItemDetails} from "../../actions/inventoryActions";
import { UPDATE_ITEM_RESET } from "../../constants/inventoryConstant";

const UpdateItem = () => {
  const dispatch = useDispatch();
  const {id} = useParams();
 // const navigate = useNavigate();

 const tomorrow = new Date();
  tomorrow.setDate(tomorrow.getDate() + 1);
  const tomorrowFormatted = tomorrow.toISOString().split('T')[0];

  const { loading, isUpdated, error } = useSelector(
    (state) => state.itemProfile
  );
  const {
    loading: itemLoading,
    error: itemError,
    item,
  } = useSelector((state) => state.itemDetails);

  const [name, setName] = useState();
  const [lab, setLab] = useState();
  const [cost, setCost] = useState();
  const [damaged,setDamaged] = useState();
  const [dateOfPurchase,setDateOfPurchase] = useState();
  const [password,setPassword] = useState();

  const labs = [
    "BOTANY",
    "ZOOLOGY",
    "PHYSICS",
    "CHEMISTRY",
    "IT",
    "MESS",
    "INFRASTRUCTURE"
  ]

  const updateSubmit = (e) => {
    e.preventDefault();
    try {
      const data = {
        name,
        lab,
        cost,
        password,
        dateOfPurchase,
        damaged
      };
      dispatch(updateItem(id,data));
    } catch (err) {}
  };

  const success = () => {
    toast.success("Item Updated Successfully");
  };
  const failure = (error) => {
    toast.error(error);
  };
  useEffect(() => {
    if(!item || (item && item._id !== id)) dispatch(getItemDetails(id));
    else{
        setName(item.name);
        setCost(item.cost);
        setLab(item.lab);
        setDamaged(item.damaged);
        setDateOfPurchase(item.dateOfPurchase);
    }
    if (error) {
      failure(error);
      dispatch(clearErrors());
    }

    if (error) {
      failure(error);
      dispatch(clearErrors());
    }

    if (isUpdated) {
      //  alert.success("Profile updated successfully");
      success();
      dispatch({ type: UPDATE_ITEM_RESET });
      dispatch(getItemDetails(id));
    }
  }, [dispatch, error, isUpdated,id,item]);

  return (
    <Fragment>
      {loading ? (
        <Loader />
      ) : (
        <div className="cardUPR">
          <InventorySidebar />
          <p>Update item</p>
          <form className="loginForm" onSubmit={updateSubmit}>
            <div className="loginPhone">
              <input
                type="text"
                placeholder="name"
                required
                value={name}
                onChange={(e) => setName(e.target.value)}
              />
            </div>
            <div className="password">
              <input
                type="number"
                placeholder="cost"
                required
                value={cost}
                onChange={(e) => setCost(e.target.value)}
              />
            </div>
            <div className="password">
              <input
                type="number"
                placeholder="damaged"
                required
                value={damaged}
                onChange={(e) => setDamaged(e.target.value)}
              />
            </div>
            <div>
              <select onChange={(e) => setLab(e.target.value)}>
                <option value="">{lab ? lab : "Select Lab"}</option>
                {labs.map((cate) => (
                  <option key={cate} value={cate}>
                    {cate}
                  </option>
                ))}
              </select>
            </div>
            <div className="password">
                <label className="checks">
                 <p>Date Of Initial Purchase : </p> 
                <input
                  type="date"
                  placeholder="date of purchase"
                  required
                  value={dateOfPurchase}
                  onChange={(e) => setDateOfPurchase(e.target.value)}
                  max={tomorrowFormatted}
                />
                </label>
            </div>
            <div className="password">
              <input
                type="password"
                placeholder="password"
                required
                value={password}
                onChange={(e) => setPassword(e.target.value)}
              />
            </div>
            <input type="submit" value="Update" className="loginBtn" />
          </form>
        </div>
      )}
    </Fragment>
  );
};
export default UpdateItem;
