import React, { Fragment, useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import { toast } from "react-toastify";
import "react-js-dialog-box/dist/index.css";

import "../Books/BookDetails.css";
import {
  getItemDetails,
  clearErrors,
} from "../../actions/inventoryActions";
import InventorySidebar from "../layout/Loader/InventorySidebar";

const ItemDetails = () => {
  const dispatch = useDispatch();
  const { id } = useParams();

  const { error, item } = useSelector((state) => state.itemDetails);

  const failure = (error) => {
    toast.error(error);
  };

  useEffect(() => {
    if (error) {
      failure(error);
      dispatch(clearErrors());
    }

    dispatch(getItemDetails(id));
  }, [dispatch,error,id]);

  return (
    <Fragment>
      <InventorySidebar/>
      <div className="detailsContainer">
        <h2 className="bookName">{item.name}</h2>
        <div className="details">
          <span className="prefix">Lab : </span>
          <span className="value">{item.lab}</span>
        </div>
        <div className="details">
          <span className="prefix">Cost : </span>
          <span className="value">{item.cost}</span>
        </div>
        <div className="details">
          <span className="prefix">Stock : </span>
          <span className="value">{item.stock}</span>
        </div>
        <div className="details">
          <span className="prefix">date of purchase : </span>
          <span className="value">{item.dateOfPurchase}</span>
        </div>
        <div className="details">
          <span className="prefix">Damaged: </span>
          <span className="value">{item.damaged}</span>
        </div>
        <div className="booksToReturn">
          {item.log && item.log.map((log) => {
            return (
              <div className="books">
                <div className={log.category == "ADD" ? "greenClasss" : "redClasss"}>{log.category}</div>
                <div className="details">
                  <span className="spanD">approved by : </span>
                  <span>{log.approvedBy}</span>
                </div>
                <div className="details">
                  <span className="spanD">asked by : </span>
                  <span>{log.askedBy ? log.askedBy : log.approvedBy}</span>
                </div>
                <div className="details">
                  <span className="spanD">stock : </span>
                  <span>{log.stock}</span>
                </div>
                <div className="details">
                  <span className="spanD">cost : </span>
                  <span>{log.cost}</span>
                </div>
                <div className="details">
                  <span className="spanD">Total Cost : </span>
                  <span>{log.totalCost}</span>
                </div>
                <div className="details">
                  <span className="spanD">IssuedOn : </span>
                  <span className="bottomS">
                    {log.date && log.date.substr(0, 10)}
                  </span>
                </div>
              </div>
            );
          })}
        </div>
      </div>
    </Fragment>
  );
};

export default ItemDetails;
