import React from "react";
import "./Sidebar.css";
import { Link } from "react-router-dom";
import logo from "../../../images/collegeLogo.png";

const LibrarySidebar = () => {

  return (
    <div className="sidebar">
      <Link to="/library">
        <button>Home</button>
      </Link>
      <Link to="/book/create">
        <button>
          Create Book
        </button>
      </Link>
      <Link to="/user/create">
        <button>
          Create User
        </button>
      </Link>
      <Link to="/book/search">
        <button>
          Search Book
        </button>
      </Link>
      <Link to="/user/search">
        <button>
          Search User
        </button>
      </Link>
      <Link to="/book/withdraw">
        <button>
          Withdraw Book
        </button>
      </Link>
      <Link to="/book/return">
        <button>
          Return Book
        </button>
      </Link>
    </div>
  );
};

export default LibrarySidebar;