import React, { Fragment, useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import { toast } from "react-toastify";
import { ReactDialogBox } from "react-js-dialog-box";
import "react-js-dialog-box/dist/index.css";

import "../Books/BookDetails.css";
import { getAllBooks } from "../../actions/booksActions";
import {
  getUserDetails,
  clearErrors,
  payFine,
} from "../../actions/usersActions";
import { FINE_USER_RESET } from "../../constants/usersConstant";
import LibrarySidebar from "../layout/Loader/LibrarySidebar";

const UserDetails = () => {
  const dispatch = useDispatch();
  const { id } = useParams();

  const { error, user } = useSelector((state) => state.userDetails);
  const { error: booksError, books } = useSelector((state) => state.allBooks);
  const { error: fineError, isFined } = useSelector(
    (state) => state.userProfile
  );

  const [isOpen, setIsOpen] = useState(false);
  const [password, setPassword] = useState();
  const [num, setNum] = useState();

  const openDialog = () => {
    setIsOpen(true);
  };

  const closeDialog = () => {
    setPassword();
    setIsOpen(false);
  };

  const makeNum = (numb) => {
    setNum(numb);
  };

  const deleteSubmit = (e) => {
    e.preventDefault();
    const temp = password;
    closeDialog();
    try {
      dispatch(payFine({ number: num, password: temp, id }));
    } catch (err) {}
  };

  const successFine = () => {
    toast.success("Fine Paid Successfully");
  };

  const failure = (error) => {
    toast.error(error);
  };

  const arr = user.yetToReturn ? user.yetToReturn.map((ele) => String(ele.book)) : [];
  console.log(arr);
  const booksYetToReturn = [];
  books &&
    books.forEach((book) => {
      if (arr.includes(book.number)) booksYetToReturn.push(book);
    });

    console.log(booksYetToReturn);

  useEffect(() => {
    if (isFined === true) {
      successFine();
      dispatch({ type: FINE_USER_RESET });
    }
    if (error) {
      failure(error);
      dispatch(clearErrors());
    }
    if (booksError) {
      failure(booksError);
      dispatch(clearErrors());
    }
    if (fineError) {
      failure(fineError);
      dispatch(clearErrors());
    }

    dispatch(getAllBooks());
    dispatch(getUserDetails(id));
  }, [dispatch,isFined,error,fineError,booksError,id]);

  return (
    <Fragment>
      {isOpen && (
        <ReactDialogBox
          closeBox={closeDialog}
          modalWidth="28%"
          headerBackgroundColor="rgb(192, 188, 188)"
          headerTextColor="rgb(39, 39, 39)"
          headerHeight="50"
          closeButtonColor="#D92736"
          bodyBackgroundColor="white"
          bodyTextColor="black"
          bodyHeight="35%"
          headerText="Enter Admin Password"
        >
          <div>
            <form className="loginForm" onSubmit={deleteSubmit}>
              <div className="password">
                <input
                  type="password"
                  placeholder="password"
                  value={password}
                  onChange={(e) => setPassword(e.target.value)}
                ></input>
              </div>
              <button type="submit" className="loginBtn">
                PaidFine
              </button>
            </form>
          </div>
        </ReactDialogBox>
      )}
      <LibrarySidebar/>
      <div className="detailsContainer">
        <h2 className="bookName">{user.name}</h2>
        <div className="details">
          <span className="prefix">Phone Number : </span>
          <span className="value">{user.phone}</span>
        </div>
        <div className="details">
          <span className="prefix">Books Issued : </span>
          <span className="value">{user.issues}</span>
        </div>
        <div className="details">
          <span className="prefix">Books Returned : </span>
          <span className="value">{user.returns}</span>
        </div>
        <div className="details">
          <span className="prefix">Books Yet to Return : </span>
          <span className="value">
            {user.yetToReturn && user.yetToReturn.length}
          </span>
        </div>
        <div className="booksToReturn">
          {booksYetToReturn.map((book) => {
            return (
              <div className="books">
                <div className="headingb">{book.name}</div>
                <div className="details">
                  <span className="spanD">number id : </span>
                  <span>{book.number}</span>
                </div>
                <div className="details">
                  <span className="spanD">fine : </span>
                  <span className="spanFine">{book.cost + 50}</span>
                </div>
                <div className="details">
                  <span className="spanD">IssuedBy : </span>
                  <span>{book.lastGiver}</span>
                </div>
                <div className="details">
                  <span className="spanD">IssuedOn : </span>
                  <span>
                    {book.lastIssuedOn && book.lastIssuedOn.substr(0, 10)}
                  </span>
                </div>
                <button
                  className="fineBtn"
                  onClick={() => {
                    makeNum(book.number);
                    openDialog();
                  }}
                >
                  pay fine
                </button>
              </div>
            );
          })}
        </div>
      </div>
    </Fragment>
  );
};

export default UserDetails;
