import React,{useEffect,useState} from "react";
import { useParams } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import {toast} from "react-toastify"; 
import  {ReactDialogBox}  from "react-js-dialog-box";

import "../Books/BookDetails.css"
import MisSidebar from "../layout/Loader/MisSidebar";
import { getStaffDetails, clearErrors, updateStaff } from "../../actions/staffActions";
import { UPDATE_STAFF_RESET } from "../../constants/staffConstant";
import PDFViewer from "../Utils/PDFViewer.js";

const StaffDetails = () => {
  const dispatch = useDispatch();
  const {id} = useParams();

  const {error,staff} = useSelector(state => state.staffDetails);
  const {isUpdated,error:updateError} = useSelector((state) => state.staffProfile)
  const [isOpen, setIsOpen] = useState(false);
  const [password, setPassword] = useState();

  const openDialog = () => {
    setIsOpen(true);
  };

  const closeDialog = () => {
    setPassword();
    setIsOpen(false);
  };

  const reduceSubmit = (e) => {
    e.preventDefault();
    const temp = password;
    closeDialog();
    const data = {
      password:temp,
      leavesUsed: staff.leavesUsed + 1
    }
    try {
      dispatch(updateStaff(id,data));
    } catch (err) {}
  };

  const success = () => {
    toast.success("Leave added Successfully");
  };
    const failure = (error) => {
      toast.error(error);
    };

    const monthsSalaryLeft = (dateString) => {
        const date1 = new Date(dateString);
        const date2 = new Date(Date.now());
      
        // Calculate the difference in months
        const monthsDifference = (date2.getFullYear() - date1.getFullYear()) * 12 + (date2.getMonth() - date1.getMonth())
         + ((date2.getDate() < date1.getDate() && date2.getMonth() > date1.getMonth()) ? -1 : 0);
      
        return monthsDifference;
    }

    const monthsLeft = staff && staff.lastMonthPaid ? monthsSalaryLeft(staff.lastMonthPaid) : 0;
    const moneyToDeduct = staff.leavesUsed > staff.leavesAllowed ? (staff.leavesUsed - staff.leavesAllowed)*(Math.round(staff.salary/30)) : 0;
      

    useEffect(()=>{
        if(error || updateError){
            failure(error);
            dispatch(clearErrors());     
        }
        if(isUpdated){
           success();
           dispatch({type:UPDATE_STAFF_RESET});
        }
       dispatch(getStaffDetails(id));
    },[dispatch,isUpdated,id])

    return (
      <>
       {isOpen && (
        <ReactDialogBox
          closeBox={closeDialog}
          modalWidth="28%"
          headerBackgroundColor="rgb(192, 188, 188)"
          headerTextColor="rgb(39, 39, 39)"
          headerHeight="50"
          closeButtonColor="#D92736"
          bodyBackgroundColor="white"
          bodyTextColor="black"
          bodyHeight="35%"
          headerText="Enter Admin Password"
        >
          <div>
            <form className="loginForm" onSubmit={reduceSubmit}>
              <div className="password">
                <input
                  type="password"
                  placeholder="password"
                  value={password}
                  onChange={(e) => setPassword(e.target.value)}
                ></input>
              </div>
              <button type="submit" className="loginBtn">
                Reduce
              </button>
            </form>
          </div>
        </ReactDialogBox>
      )}
      <MisSidebar/>
      <div className="detailsContainer">
        <h2 className="bookName">{staff.name}</h2>
        <div className="details">
          <span className="prefix">Phone Number : </span>
          <span className="value">{staff.phone}</span>
        </div>
        <div className="details">
          <span className="prefix">Salary : </span>
          <span className="value">{staff.salary}</span>
        </div>
        <div className="details">
          <span className="prefix">Qualification : </span>
          <span className="value">{staff.qualification}</span>
        </div>
        <div className="details">
          <span className="prefix">Designation : </span>
          <span className="value">{staff.designation}</span>
        </div>
        <div className="details">
          <span className="prefix">Subject : </span>
          <span className="value">{staff.subject}</span>
        </div>
        <div className="details">
          <span className="prefix">leaves allowed : </span>
          <span className="value">{staff.leavesAllowed}</span>
        </div>
        <div className="details">
          <span className="prefix">leaves used : </span>
          <span className="value">{staff.leavesUsed}</span>
        </div>
        <div className="details">
          <span className="prefix">leaves remaining : </span>
          <span className="value">{staff.leavesAllowed - staff.leavesUsed}</span>
        </div>
        <div className="details">
          <span className="prefix">salary cleared till date : </span>
          <span className="value">{staff.lastMonthPaid}</span>
        </div>
        <div className="details">
          <span className="prefix">Months left to be paid : </span>
          <span className="value">{monthsLeft}</span>
        </div>
        <div className="details">
          <span className="prefix">Salary yet to be paid : </span>
          <span className="value">{Math.ceil(monthsLeft*(staff.salary) - moneyToDeduct )}</span>
        </div>
        <button
            className="fineBtn2"
            onClick={() => {
              openDialog();
            }}
        >
            reduce one leave
        </button>
        <span className="pref">Documents : </span>
        <div id="createProductFormImage">
               {staff.documents && staff.documents.map((image, index) => (
                <PDFViewer pdfUrl = {image.url} key = {index}/>
               ))}
        </div>
      </div>
      </>
    );
}

export default StaffDetails;