import React, { Fragment, useEffect, useState } from "react";
import { useSelector, useDispatch } from "react-redux";
import DataTable from "react-data-table-component";
import { ReactDialogBox } from "react-js-dialog-box";
import "react-js-dialog-box/dist/index.css";

import "../Books/SearchBook.css";
import Loader from "../layout/Loader/Loader";
import {
  clearErrors,
  getAllCollegeStudents,
  deleteCollegeStudent,
} from "../../actions/collegeStudentActions";
import { toast } from "react-toastify";
import { DELETE_CSTUDENT_RESET } from "../../constants/collegeStudentConstant";
import FeeSidebar from "../layout/Loader/FeeSidebar";

const SearchCollegeStudent = () => {
  const dispatch = useDispatch();

  const { error, collegeStudents,loading } = useSelector((state) => state.allCollegeStudents);

  const { error: deleteError, isDeleted } = useSelector(
    (state) => state.collegeStudentProfile
  );

  const [isOpen, setIsOpen] = useState(false);
  const [password, setPassword] = useState();
  const [id, setId] = useState();
  const [value, setValue] = useState(); 
  const [option,setOption] = useState();
  
  
  let rollNoSort = 0;
  let nameSort = 0;
  let semesterSort = 0;
  let feeDueSort = 0;

  const options = ["name", "degree", "rollNo","stream","hostel","phone","semester"];
  
  const openDialog = () => {
    setIsOpen(true);
  };

  const closeDialog = () => {
    setPassword();
    setIsOpen(false);
  };

  const makeId = (id) => {
    setId(id);
  };

  const deleteSubmit = () => {
    const temp = password;
    closeDialog();
    dispatch(deleteCollegeStudent(id, temp));
  };

  const successDelete = () => {
    toast.success("Student Deleted Successfully");
  };
  const failure = (error) => {
    toast.error(error);
  };


  const sortFunction = (type) => {
    if (type === "rollNo") {
      rollNoSort++;
      if (rollNoSort % 2 === 0) collegeStudents.sort((a, b) => b.rollNo - a.rollNo);
      else collegeStudents.sort((a, b) => a.rollNo - b.rollNo);
    } 
    
    else if (type === "name") {
      nameSort++;
      if (nameSort % 2 === 0) collegeStudents.sort((a, b) => b.name.localeCompare(a.name));
      else collegeStudents.sort((a, b) => a.name.localeCompare(b.name));
    } 
    
    else if (type === "feeDue") {
      feeDueSort++;
      if (feeDueSort % 2 === 0) collegeStudents.sort((a, b) => b.feeLeft - a.feeLeft);
      else collegeStudents.sort((a, b) => a.feeLeft - b.feeLeft);
    }
    
    else if (type === "semester") {
      semesterSort++;
      if (semesterSort % 2 === 0)
        collegeStudents.sort((a, b) => b.semester - a.semester);
      else collegeStudents.sort((a, b) => a.semester - b.semester);
    }

  };

  {collegeStudents && collegeStudents.forEach((collegeStudent,index) => {
    collegeStudent.num = index + 1    
  })}

  const columns = [ 
    {
      name: (
        <h3 className="headings">
          Serial No.
        </h3>
      ),
      selector: (row) => <span className="cellData">{row.num}</span>,
    },
    {
      name: (
        <h3 className="headings" onClick={() => sortFunction("rollNo")}>
          Roll Number
        </h3>
      ),
      selector: (row) => <span className="cellData">{row.rollNo}</span>,
      sortable: true,
    },
    {
      name: (
        <h3 className="headings" onClick={() => sortFunction("name")}>
          Name
        </h3>
      ),
      selector: (row) => (
        <a className="cellLink" href={`/collegeStudent/${row._id}`}>
          <p>{row.name}</p>
        </a>
      ),
      sortable: true,
      wrap: true,
    },
    {
      name: (
        <h3 className="headings" >
          Degree
        </h3>
      ),
      selector: (row) => <span className="cellData">{row.degree}</span>,
      sortable: true,
      wrap: true,
    },
    {
      name: (
        <h3 className="headings" >
          Phone
        </h3>
      ),
      selector: (row) => <span className="cellData">{row.phone}</span>,
      sortable: true,
      wrap: true,
    },
    {
      name: (
        <h3 className="headings" >
          Son/Daughter Of
        </h3>
      ),
      selector: (row) => <span className="cellData">{row.sonOf.name}</span>,
      sortable: true,
      wrap: true,
    },
    {
      name: <h4 className="headings">Stream</h4>,
      selector: (row) => (
        <span
          className="cellData"
        >
          {row.stream}
        </span>
      ),
      wrap: true,
    },
    {
      name: <h4 className="headings" onClick={()=>sortFunction("semester")}>semester</h4>,
      selector: (row) => <span className="cellData">{row.semester}</span>,
      wrap: true,
      sortable:true,
    },
    {
      name: <h4 className="headings">address</h4>,
      selector: (row) => <span className="cellData">{row.address}</span>,
      wrap: true,
    },
    {
      name: <h4 className="headings">Hostel</h4>,
      selector: (row) => (
        <span
          className="cellData"
        >
          {row.hostel === true ? "yes" : "no"}
        </span>
      ),
      wrap: true,
    },
    {
      name: (
        <h4 className="headings" onClick={() => sortFunction("feeDue")}>
          Fee Due
        </h4>
      ),
      selector: (row) => <span className="cellData">{row.feeLeft}</span>,
      sortable: true,
      wrap: true,
    },
    {
      name: "",
      cell: (row) => (
        <a className="rlinks" href={`/update/collegeStudent/${row._id}`}>
          <button className="btn">Edit</button>
        </a>
      ),
    },
    {
      name: "",
      cell: (row) => (
        <button
          onClick={() => {
            openDialog();
            makeId(row._id);
          }}
          className="delBtn"
        >
          Delete
        </button>
      ),
    },
  ]; 

  
  const searchSubmit = (e) => {
    e.preventDefault();
    dispatch(getAllCollegeStudents(option, value));
  };


  useEffect(() => {
    if (error) {
      failure(error);
      dispatch(clearErrors());
    }

    if (deleteError) {
      failure(deleteError);
      dispatch(clearErrors());
    }

    if (isDeleted === true) {
      successDelete();
      dispatch({ type: DELETE_CSTUDENT_RESET });
    }

    dispatch(getAllCollegeStudents());
  }, [dispatch, error, deleteError, isDeleted]);


  return (
    <Fragment>
     
      {isOpen && (
        <ReactDialogBox
          closeBox={closeDialog}
          modalWidth="28%"
          headerBackgroundColor="rgb(192, 188, 188)"
          headerTextColor="rgb(39, 39, 39)"
          headerHeight="50"
          closeButtonColor="#D92736"
          bodyBackgroundColor="white"
          bodyTextColor="black"
          bodyHeight="35%"
          headerText="Enter Admin Password"
        >
          <div>
            <form className="loginForm" onSubmit={deleteSubmit}>
              <div className="password">
                <input
                  type="password"
                  placeholder="password"
                  value={password}
                  onChange={(e) => setPassword(e.target.value)}
                ></input>
              </div>
              <button type="submit" className="loginBtn">
                delete
              </button>
            </form>
          </div>
        </ReactDialogBox>
      )}
      <FeeSidebar/>
      <div className="dashboard">
        <div className="productListContainer">
          <h1 id="productListHeading">Modern College degree students</h1>
          <div className="searchBox">
            <select
              onChange={(e) => {
                setValue("");
                setOption(e.target.value);
              }}
              className="searchType"
            >
              <option value="">Select</option>
              {options.map((cate) => (
                <option key={cate} value={cate}>
                  {cate}
                </option>
              ))}
            </select>
            <div>
              <form className="searchForm" onSubmit={searchSubmit}>
                <input
                  type="text"
                  className="searchInput"
                  value={value}
                  onChange={(e) => {
                    setValue(e.target.value);
                  }}
                />
                <button type="submit" className="searchBtn">
                  Search
                </button>
              </form>
            </div>
          </div>
          <DataTable
            pagination
            columns={columns}
            data={collegeStudents}
            className="productListTable"
            highlightOnHover={true}
            persistTableHead={true}
            fixedHeader={true}
          />
        </div>
      </div>
    </Fragment>
  );
};

export default SearchCollegeStudent;
