import React, { useEffect } from "react";
import InventorySidebar from "../layout/Loader/InventorySidebar";
import logo from "../../images/collegeLogo.png";
import { useDispatch, useSelector } from "react-redux";

import "./LibraryHome.css";
import Loader from "../layout/Loader/Loader.js";
import { getAllItems } from "../../actions/inventoryActions.js";


function InventoryHome() {
    
    const dispatch = useDispatch();
    const {items,loading} = useSelector(state => state.allItems);

    let botany = 0;
    let zoology = 0;
    let mess = 0;
    let infra = 0;
    let physics = 0;
    let chem = 0;
    let it = 0;

    try{
          items.forEach((item) => {
            if(item.lab == "BOTANY") botany += Number(item.stock*item.cost);
            else if(item.lab == "ZOOLOGY") zoology += Number(item.stock*item.cost);
            else if(item.lab == "CHEMISTRY") chem += Number(item.stock*item.cost);
            else if(item.lab == "MESS") mess += Number(item.stock*item.cost);
            else if(item.lab == "IT") it += Number(item.stock*item.cost);
            else if(item.lab == "PHYSICS") physics += Number(item.stock*item.cost);
            else infra += Number(item.stock*item.cost);
          })
    }
    catch(err){
        console.log(err);
    }

    useEffect(()=>{
      dispatch(getAllItems("",""));
    },[])


    return loading ? (<Loader/>) : (
        <>
          <InventorySidebar/>
          <div className="libContainer">
            <img src = {logo} />
            <div className="info">
              Physics lab has items worth <span className="blueClass">{physics}</span>
            </div>
            <div className="info">
              Chemistry lab has items worth <span className="blueClass">{chem}</span>
            </div>
            <div className="info">
              Botany lab has items worth <span className="blueClass">{botany}</span>
            </div>
            <div className="info">
              Zoology lab has items worth <span className="blueClass">{zoology}</span>
            </div>
            <div className="info">
              I.T lab has items worth <span className="blueClass">{it}</span>
            </div>
            <div className="info">
              Mess has items worth <span className="blueClass">{mess}</span>
            </div>
            <div className="info">
              infrastructure equipments worth<span className="blueClass">{infra}</span>
            </div>
          </div>
        </>
    )
}

export default InventoryHome;