import React, { Fragment, useEffect, useState } from "react";
import { useSelector, useDispatch } from "react-redux";
import DataTable from "react-data-table-component";
import { ReactDialogBox } from "react-js-dialog-box";
import "react-js-dialog-box/dist/index.css";

import "../Books/SearchBook.css";
import Loader from "../layout/Loader/Loader";
import { toast } from "react-toastify";
import { DELETE_STAFF_RESET } from "../../constants/staffConstant";
import MisSidebar from "../layout/Loader/MisSidebar";
import { deleteStaff,clearErrors, getAllStaff } from "../../actions/staffActions";

const SearchStaff = () => {
  const dispatch = useDispatch();

  const { error, staffs,loading } = useSelector((state) => state.allStaff);

  const { error: deleteError, isDeleted } = useSelector(
    (state) => state.staffProfile
  );

  const [isOpen, setIsOpen] = useState(false);
  const [password, setPassword] = useState();
  const [id, setId] = useState();
  const [value, setValue] = useState(); 
  const [option,setOption] = useState();
  
  
  let nameSort = 0;
  let salarySort = 0;
  let leavesRemSort = 0;
  let experienceSort = 0;
  let salaryRemSort = 0;

  const options = ["name", "phone","qualification","designation","subject"];

  const monthsSalaryLeft = (dateString) => {
    const date1 = new Date(dateString);
    const date2 = new Date(Date.now());
  
    // Calculate the difference in months
    const monthsDifference = (date2.getFullYear() - date1.getFullYear()) * 12 + (date2.getMonth() - date1.getMonth())
     + ((date2.getDate() < date1.getDate() && date2.getMonth() > date1.getMonth()) ? -1 : 0);
  
    return monthsDifference;
   }

   {staffs && staffs.forEach((staff) => {
    staff.salaryRem = staff.lastMonthPaid ? monthsSalaryLeft(staff.lastMonthPaid)*(staff.salary) : 0;
    staff.salaryRem -= (staff.leavesUsed > staff.leavesAllowed ? (staff.leavesUsed - staff.leavesAllowed)*(Math.round(staff.salary/30)) : 0);
   })}
  
  const openDialog = () => {
    setIsOpen(true);
  };

  const closeDialog = () => {
    setPassword();
    setIsOpen(false);
  };

  const makeId = (id) => {
    setId(id);
  };

  const deleteSubmit = () => {
    const temp = password;
    closeDialog();
    dispatch(deleteStaff(id, temp));
  };

  const successDelete = () => {
    toast.success("Staff Deleted Successfully");
  };
  const failure = (error) => {
    toast.error(error);
  };


  const sortFunction = (type) => {
    if (type === "salary") {
      salarySort++;
      if (salarySort % 2 === 0) staffs.sort((a, b) => b.salary - a.salary);
      else staffs.sort((a, b) => a.salary - b.salary);
    } 
    
    else if (type === "name") {
      nameSort++;
      if (nameSort % 2 === 0) staffs.sort((a, b) => b.name.localeCompare(a.name));
      else staffs.sort((a, b) => a.name.localeCompare(b.name));
    } 
    
    else if (type === "leavesRem") {
      leavesRemSort++;
      if (leavesRemSort % 2 === 0) staffs.sort((a, b) => (b.leavesAllowed - b.leavesUsed)- (a.leavesAllowed - a.leavesUsed));
      else staffs.sort((a, b) => (a.leavesAllowed - a.leavesUsed) - (b.leavesAllowed - b.leavesUsed));
    }
    
    else if (type === "experience") {
      experienceSort++;
      if (experienceSort % 2 === 0)
        staffs.sort((a, b) => b.experience - a.experience);
      else staffs.sort((a, b) => a.experience - b.experience);
    }

    else if(type === "salaryRem"){
      salaryRemSort++;
      if(salaryRemSort%2 === 0) staffs.sort((a,b) => b.salaryRem - a.salaryRem);
      else staffs.sort((a,b) => a.salaryRem - b.salaryRem);
    }

  };

  {staffs && staffs.forEach((staff,index) => {
    staff.num = index + 1    
  })}

  const columns = [
    {
        name: (
          <h4 className="headings">
            Serial No.
          </h4>
        ),
        selector: (row) => <span className="cellData">{row.num}</span>,
      },
    {
      name: (
        <h3 className="headings" onClick={() => sortFunction("name")}>
          Name
        </h3>
      ),
      selector: (row) => (
        <a className="cellLink" href={`/staff/${row._id}`}>
          <p>{row.name}</p>
        </a>
      ),
      sortable: true,
      wrap: true,
    },
    {
      name: (
        <h3 className="headings" >
          Phone
        </h3>
      ),
      selector: (row) => <span className="cellData">{row.phone}</span>,
      sortable: true,
      wrap: true,
    },
    {
      name: (
        <h3 className="headings" onClick={() => sortFunction("salary")}>
          Salary
        </h3>
      ),
      selector: (row) => <span className="cellData">{row.salary}</span>,
      sortable: true,
      wrap: true,
    },
    {
      name: <h4 className="headings" onClick={() => sortFunction("salaryRem")}>salary remaining</h4>,
      selector: (row) => (
        <span
          className="cellData"
        >
          {row.salaryRem}
        </span>
      ),
      wrap: true,
    },
    {
      name: <h4 className="headings" onClick={()=>sortFunction("leavesRem")}>leaves Remaining</h4>,
      selector: (row) => <span className="cellData">{row.leavesAllowed - row.leavesUsed}</span>,
      wrap: true,
      sortable:true,
    },
    {
      name: <h4 className="headings">qualification</h4>,
      selector: (row) => <span className="cellData">{row.qualification}</span>,
      wrap: true,
    },
    {
        name: <h4 className="headings" onClick={()=>sortFunction("experience")}>experience</h4>,
        selector: (row) => <span className="cellData">{row.experience}</span>,
        wrap: true,
        sortable:true,
    },
    {
        name: <h4 className="headings">designation</h4>,
        selector: (row) => <span className="cellData">{row.designation}</span>,
        wrap: true,
    }, 
    {
        name: <h4 className="headings">Salary Cleared Till</h4>,
        selector: (row) => <span className="cellData">{row.lastMonthPaid}</span>,
        wrap: true,
    }, 
    {
        name: <h4 className="headings">subject</h4>,
        selector: (row) => <span className="cellData">{row.subject}</span>,
        wrap: true,
    }, 
    {
      name: "",
      cell: (row) => (
        <a className="rlinks" href={`/update/staff/${row._id}`}>
          <button className="btn">Edit</button>
        </a>
      ),
    },
    {
      name: "",
      cell: (row) => (
        <button
          onClick={() => {
            openDialog();
            makeId(row._id);
          }}
          className="delBtn"
        >
          Delete
        </button>
      ),
    },
  ]; 

  
  const searchSubmit = (e) => {
    e.preventDefault();
    dispatch(getAllStaff(option, value));
  };


  useEffect(() => {
    if (error) {
      failure(error);
      dispatch(clearErrors());
    }

    if (deleteError) {
      failure(deleteError);
      dispatch(clearErrors());
    }

    if (isDeleted === true) {
      successDelete();
      dispatch({ type: DELETE_STAFF_RESET });
    }

    dispatch(getAllStaff());
  }, [dispatch, error, deleteError, isDeleted]);


  return (
    <Fragment>
     
      {isOpen && (
        <ReactDialogBox
          closeBox={closeDialog}
          modalWidth="28%"
          headerBackgroundColor="rgb(192, 188, 188)"
          headerTextColor="rgb(39, 39, 39)"
          headerHeight="50"
          closeButtonColor="#D92736"
          bodyBackgroundColor="white"
          bodyTextColor="black"
          bodyHeight="35%"
          headerText="Enter Admin Password"
        >
          <div>
            <form className="loginForm" onSubmit={deleteSubmit}>
              <div className="password">
                <input
                  type="password"
                  placeholder="password"
                  value={password}
                  onChange={(e) => setPassword(e.target.value)}
                ></input>
              </div>
              <button type="submit" className="loginBtn">
                delete
              </button>
            </form>
          </div>
        </ReactDialogBox>
      )}
      <MisSidebar/>
      <div className="dashboard">
        <div className="productListContainer">
          <h1 id="productListHeading">Modern College Staff</h1>
          <div className="searchBox">
            <select
              onChange={(e) => {
                setValue("");
                setOption(e.target.value);
              }}
              className="searchType"
            >
              <option value="">Select</option>
              {options.map((cate) => (
                <option key={cate} value={cate}>
                  {cate}
                </option>
              ))}
            </select>
            <div>
              <form className="searchForm" onSubmit={searchSubmit}>
                <input
                  type="text"
                  className="searchInput"
                  value={value}
                  onChange={(e) => {
                    setValue(e.target.value);
                  }}
                />
                <button type="submit" className="searchBtn">
                  Search
                </button>
              </form>
            </div>
          </div>
          <DataTable
            pagination
            columns={columns}
            data={staffs}
            className="productListTable"
            highlightOnHover={true}
            persistTableHead={true}
            fixedHeader={true}
          />
        </div>
      </div>
    </Fragment>
  );
};

export default SearchStaff;
