import React, { Fragment, useEffect, useState } from "react";
import { useSelector, useDispatch } from "react-redux";
import DataTable from "react-data-table-component";
import { ReactDialogBox } from "react-js-dialog-box";
import "react-js-dialog-box/dist/index.css";

import "./SearchBook.css";
import {
  clearErrors,
  getAllBooks,
  deleteBook,
} from "../../actions/booksActions";
import { toast } from "react-toastify";
import { DELETE_BOOK_RESET } from "../../constants/booksConstant";
import LibrarySidebar from "../layout/Loader/LibrarySidebar";

const SearchBook = () => {
  const dispatch = useDispatch();

  const { error, books } = useSelector((state) => state.allBooks);

  const { error: deleteError, isDeleted } = useSelector(
    (state) => state.bookProfile
  );

  const [isOpen, setIsOpen] = useState(false);
  const [password, setPassword] = useState();
  const [id, setId] = useState();
  const [category, setCategory] = useState();
  const [value, setValue] = useState(); 
  const [option,setOption] = useState();
  
  
  let numSort = 0;
  let nameSort = 0;
  let authorSort = 0;
  let costSort = 0;
  let yearOfEditionSort = 0;
  let categorySort = 0;

  const options = ["name", "author", "number","category"];
  
  const openDialog = () => {
    setIsOpen(true);
  };

  const closeDialog = () => {
    setPassword();
    setIsOpen(false);
  };

  const makeId = (id) => {
    setId(id);
  };

  const deleteSubmit = () => {
    const temp = password;
    closeDialog();
    dispatch(deleteBook(id, temp));
  };

  const successDelete = () => {
    toast.success("Book Deleted Successfully");
  };
  const failure = (error) => {
    toast.error(error);
  };


  const sortFunction = (type) => {
    if (type === "number") {
      numSort++;
      if (numSort % 2 === 0) books.sort((a, b) => b.number - a.number);
      else books.sort((a, b) => a.number - b.number);
    } 
    
    else if (type === "name") {
      nameSort++;
      if (nameSort % 2 === 0) books.sort((a, b) => b.name.localeCompare(a.name));
      else books.sort((a, b) => a.name.localeCompare(b.name));
    } 
    
    else if (type === "author") {
      authorSort++;
      if (authorSort % 2 === 0) books.sort((a, b) => b.author.localeCompare(a.author));
      else books.sort((a, b) => a.author.localeCompare(b.author));
    } 
    
    else if (type === "cost") {
      costSort++;
      if (costSort % 2 === 0) books.sort((a, b) => b.cost - a.cost);
      else books.sort((a, b) => a.cost - b.cost);
    }
    
    else if (type === "yearOfEdition") {
      yearOfEditionSort++;
      if (yearOfEditionSort % 2 === 0)
        books.sort((a, b) => b.yearOfEdition - a.yearOfEdition);
      else books.sort((a, b) => a.yearOfEdition - b.yearOfEdition);
    }

    else if (type === "category"){
             categorySort++;
             if (categorySort % 2 === 0)
               books.sort((a, b) => b.category.localeCompare(a.category));
             else books.sort((a, b) => a.category.localeCompare(b.category));
    }
  };

  {books && books.forEach((staff,index) => {
    staff.num = index + 1    
  })}

  const columns = [
    {
      name: (
        <h3 className="headings">
          Serial No.
        </h3>
      ),
      selector: (row) => <span className="cellData">{row.num}</span>,
    },
    {
      name: (
        <h3 className="headings" onClick={() => sortFunction("number")}>
          Number-Id
        </h3>
      ),
      selector: (row) => <span className="cellData">{row.number}</span>,
      sortable: true,
    },
    {
      name: (
        <h3 className="headings" onClick={() => sortFunction("name")}>
          Name
        </h3>
      ),
      selector: (row) => (
        <a className="cellLink" href={`/book/${row._id}`}>
          <p>{row.name}</p>
        </a>
      ),
      sortable: true,
      wrap: true,
    },
    {
      name: (
        <h3 className="headings" onClick={() => sortFunction("author")}>
          Author
        </h3>
      ),
      selector: (row) => <span className="cellData">{row.author}</span>,
      sortable: true,
      wrap: true,
    },
    {
      name: (
        <h3 className="headings" onClick={() => sortFunction("category")}>
          Category
        </h3>
      ),
      selector: (row) => <span className="cellData">{row.category}</span>,
      sortable: true,
      wrap: true,
    },
    {
      name: (
        <h3 className="headings" onClick={() => sortFunction("cost")}>
          Price
        </h3>
      ),
      selector: (row) => <span className="cellData">{row.cost}</span>,
      sortable: true,
      wrap: true,
    },
    {
      name: <h4 className="headings">currently with</h4>,
      selector: (row) => (
        <span
          className={`cellData ${
            row.bookWith === "library" ? "greenClass" : "redClass"
          }`}
        >
          {row.bookWith}
        </span>
      ),
      wrap: true,
    },
    {
      name: <h4 className="headings">last acceptor</h4>,
      selector: (row) => <span className="cellData">{row.lastAcceptor}</span>,
      wrap: true,
    },
    {
      name: <h4 className="headings">last giver</h4>,
      selector: (row) => <span className="cellData">{row.lastGiver}</span>,
      wrap: true,
    },
    {
      name: (
        <h4 className="headings" onClick={() => sortFunction("yearOfEdition")}>
          year of edition
        </h4>
      ),
      selector: (row) => <span className="cellData">{row.yearOfEdition}</span>,
      sortable: true,
      wrap: true,
    },
    {
      name: "",
      cell: (row) => (
        <a className="rlinks" href={`/update/book/${row._id}`}>
          <button className="btn">Edit</button>
        </a>
      ),
    },
    {
      name: "",
      cell: (row) => (
        <button
          onClick={() => {
            openDialog();
            makeId(row._id);
          }}
          className="delBtn"
        >
          Delete
        </button>
      ),
    },
  ]; 

  
  const searchSubmit = (e) => {
    e.preventDefault();
    dispatch(getAllBooks(option, value));
  };


  useEffect(() => {
    if (error) {
      failure(error);
      dispatch(clearErrors());
    }

    if (deleteError) {
      failure(deleteError);
      dispatch(clearErrors());
    }

    if (isDeleted === true) {
      successDelete();
      dispatch({ type: DELETE_BOOK_RESET });
    }

    dispatch(getAllBooks());
  }, [dispatch, error, deleteError, isDeleted]);


  return (
    <Fragment>
      {isOpen && (
        <ReactDialogBox
          closeBox={closeDialog}
          modalWidth="28%"
          headerBackgroundColor="rgb(192, 188, 188)"
          headerTextColor="rgb(39, 39, 39)"
          headerHeight="50"
          closeButtonColor="#D92736"
          bodyBackgroundColor="white"
          bodyTextColor="black"
          bodyHeight="35%"
          headerText="Enter Admin Password"
        >
          <div>
            <form className="loginForm" onSubmit={deleteSubmit}>
              <div className="password">
                <input
                  type="password"
                  placeholder="password"
                  value={password}
                  onChange={(e) => setPassword(e.target.value)}
                ></input>
              </div>
              <button type="submit" className="loginBtn">
                delete
              </button>
            </form>
          </div>
        </ReactDialogBox>
      )}
      <LibrarySidebar/>
      <div className="dashboard">
        <div className="productListContainer">
          <h1 id="productListHeading">Modern College Library Books</h1>
          <div className="searchBox">
            <select
              onChange={(e) => {
                setValue("");
                setOption(e.target.value);
              }}
              className="searchType"
            >
              <option value="">Select</option>
              {options.map((cate) => (
                <option key={cate} value={cate}>
                  {cate}
                </option>
              ))}
            </select>
            <div>
              <form className="searchForm" onSubmit={searchSubmit}>
                <input
                  type="text"
                  className="searchInput"
                  value={value}
                  onChange={(e) => {
                    setValue(e.target.value);
                  }}
                />
                <button type="submit" className="searchBtn">
                  Search
                </button>
              </form>
            </div>
          </div>
          <DataTable
            pagination
            columns={columns}
            data={books}
            className="productListTable"
            highlightOnHover={true}
            persistTableHead={true}
            fixedHeader={true}
          />
        </div>
      </div>
    </Fragment>
  );
};

export default SearchBook;
