import React, { useState, useEffect, Fragment } from "react";
import { useDispatch, useSelector } from "react-redux";
import { useNavigate, useParams } from "react-router-dom";
import { toast } from "react-toastify";

import "../Admin/UpdateProfile.css";
import Loader from "../layout/Loader/Loader";
import { UPDATE_STAFF_RESET } from "../../constants/staffConstant";
import MisSidebar from "../layout/Loader/MisSidebar";
import { getStaffDetails, updateStaff, clearErrors } from "../../actions/staffActions";

const UpdateStaff = () => {
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const { id } = useParams();

  const { loading, isUpdated, error } = useSelector(
    (state) => state.staffProfile
  );
  const {
    loading: staffLoading,
    error: staffError,
    staff,
  } = useSelector((state) => state.staffDetails);

  const [name, setName] = useState();
  const [phone, setPhone] = useState();
  const [qualification,setQualification] = useState();
  const [salary,setSalary] = useState();
  const [salaryClearedTill,setSalaryClearedTill] = useState();
  const [leavesAllowed,setLeavesAllowed] = useState();
  const [experience,setExperience] = useState();
  const [designation,setDesignation] = useState();
  const [subject,setSubject] = useState();
  const [password,setPassword] = useState();
  const [images, setImages] = useState([]);
  const [imagesPreview, setImagesPreview] = useState([]);

  const updateStaffImagesChange = (e) => {
    const files = Array.from(e.target.files);

    setImages([]);
    setImagesPreview([]);

    files.forEach((file) => {
      const reader = new FileReader();
      
      reader.readAsDataURL(file);
      reader.onload = () => {
        if (reader.readyState === 2) {
          setImagesPreview((old) => [...old, reader.result]);
          setImages((old) => [...old, reader.result]);
          //console.log("ye chla hai - 1");
        }
      };
    });
  };



  const updateSubmit = (e) => {
    e.preventDefault();
    try {
        const data = {
            name,
            phone,
            qualification,
            salary,
            leavesAllowed,
            experience,
            salaryClearedTill,
            designation,
            subject,
            images,
            password
          };
      dispatch(updateStaff(id, data));
    } catch (err) {}
  };

  const success = () => {
    toast.success("Staff Updated Successfully");
  };
  const failure = (error) => {
    toast.error(error);
  };
  useEffect(() => {
    if (!staff || (staff && staff._id !== id)) dispatch(getStaffDetails(id));
    else {
      setName(staff.name);
      setPhone(staff.phone);
      setSalary(staff.salary);
      setQualification(staff.qualification);
      setLeavesAllowed(staff.leavesAllowed);
      setDesignation(staff.designation); 
      setSubject(staff.subject);
      setSalaryClearedTill(staff.lastMonthPaid);
      setExperience(staff.experience);
    }
    if (error) {
      failure(error);
      dispatch(clearErrors());
    }
    if(staffError){
        failure(staffError);
        dispatch(clearErrors());
    }
    if (isUpdated) {
      //  alert.success("Profile updated successfully");
      success();
      navigate(`/staff/${staff._id}`);
      dispatch({ type: UPDATE_STAFF_RESET });
    }
  }, [dispatch, error, isUpdated, id, staff]);

  return (
    <Fragment>
      {loading || staffLoading ? (
        <Loader />
      ) : (
        <div className="cardUPR">
          <MisSidebar/>
          <p>Update Staff</p>
          <form className="loginForm" onSubmit={updateSubmit}>
              <div className="loginPhone">
                <input
                  type="text"
                  placeholder="name"
                  required
                  value={name}
                  onChange={(e) => setName(e.target.value)}
                />
              </div>
              <div className="password">
                <input
                  type="text"
                  placeholder="staff Phone Number"
                  required
                  value={phone}
                  onChange={(e) => setPhone(e.target.value)}
                />
              </div>
              <div className="password">
                <input
                  type="text"
                  placeholder="maximum qualification"
                  required
                  value={qualification}
                  onChange={(e) => setQualification(e.target.value)}
                />
              </div>
              <div className="password">
                <input
                  type="number"
                  placeholder="salary"
                  required
                  value={salary}
                  onChange={(e) => setSalary(e.target.value)}
                />
              </div>
              <div className="password">
                <input
                  type="number"
                  placeholder="leavesAllowed"
                  required
                  value={leavesAllowed}
                  onChange={(e) => setLeavesAllowed(e.target.value)}
                />
              </div>
              <div className="password">
                <input
                  type="number"
                  placeholder="eperience in years"
                  required
                  value={experience}
                  onChange={(e) => setExperience(e.target.value)}
                />
              </div>
              <div className="password">
                <input
                  type="text"
                  placeholder="designation"
                  required
                  value={designation}
                  onChange={(e) => setDesignation(e.target.value)}
                />
              </div>
              <div className="password">
                <input
                  type="text"
                  placeholder="subject"
                  required
                  value={subject}
                  onChange={(e) => setSubject(e.target.value)}
                />
              </div>
              <div className="password">
                <label className="checks">
                 <p>Salary Cleared till date : </p> 
                <input
                  type="date"
                  placeholder="salary cleared till"
                  required
                  value={salaryClearedTill}
                  onChange={(e) => setSalaryClearedTill(e.target.value)}
                />
                </label>
              </div>
              <div id="createProductFormFile">
                <p className="checks">choose further documents to be uploaded : </p>
                <input
                type="file"
                name="avatar"
                accept=".pdf, image/*, .docx"
                onChange={updateStaffImagesChange}
                multiple
                />
              </div>

              <div id="createProductFormImage">
               {imagesPreview.map((image, index) => (
                <img key={index} src={image} alt="Image Preview" />
               ))}
              </div>
              <div className="password">
                <input
                  type="password"
                  placeholder="admin password"
                  required
                  value={password}
                  onChange={(e) => setPassword(e.target.value)}
                />
              </div>
              <input type="submit" value="Update" className="loginBtn" />
            </form>
        </div>
      )}
    </Fragment>
  );
};
export default UpdateStaff;
