import React, { useState, useEffect, Fragment } from "react";
import { useDispatch, useSelector } from "react-redux";
import {
  clearErrors,
  getBookDetails,
  updateBook,
} from "../../actions/booksActions";
import { useNavigate,useParams } from "react-router-dom";
import { toast } from "react-toastify";

import "../Admin/UpdateProfile.css";
import Loader from "../layout/Loader/Loader";
import { UPDATE_BOOK_RESET } from "../../constants/booksConstant";
import LibrarySidebar from "../layout/Loader/LibrarySidebar";

const UpdateBook = () => {
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const {id} = useParams();
  const { loading, isUpdated, error } = useSelector(
    (state) => state.bookProfile
  );
  const {
    loading: bookLoading,
    error: bookError,
    book,
  } = useSelector((state) => state.bookDetails);

  const [author, setAuthor] = useState();
  const [category,setCategory] = useState();
  const [name, setName] = useState();
  const [cost, setCost] = useState();
  const [yearOfEdition, setYearOfEdition] = useState();
  const [password,setPassword] = useState();

  const updateSubmit = (e) => {
    e.preventDefault();
    try {
      const data = {
        author,
        category,
        name,
        cost,
        yearOfEdition,
        password
      };
      dispatch(updateBook(id,data));
    } catch (err) {}
  };

  const success = () => {
    toast.success("Book Updated Successfully");
  };
  const failure = (error) => {
    toast.error(error);
  };
  useEffect(() => {
    if(!book || (book && book._id !== id)) dispatch(getBookDetails(id));
    else{
        setAuthor(book.author);
        setName(book.name);
        setCost(book.cost);
        setYearOfEdition(book.yearOfEdition);
        setCategory(book.category);
    }
    if (error) {
      failure(error);
      dispatch(clearErrors());
    }

    if (isUpdated) {
      //  alert.success("Profile updated successfully");
      success();
      navigate(`/book/${book._id}`);
      dispatch({ type: UPDATE_BOOK_RESET });
    }
  }, [dispatch, error, isUpdated,id,book]);

  return (
    <Fragment>
      {loading ? (
        <Loader />
      ) : (
        <div className="cardUPR">
          <LibrarySidebar/>
          <p>Update Book</p>
          <form className="loginForm" onSubmit={updateSubmit}>
            <div className="loginPhone">
              <input
                type="text"
                placeholder="name"
                required
                value={name}
                onChange={(e) => setName(e.target.value)}
              />
            </div>
            <div className="Author">
              <input
                type="text"
                placeholder="Author"
                required
                value={author}
                onChange={(e) => setAuthor(e.target.value)}
              />
            </div>
            <div className="Author">
              <input
                type="text"
                placeholder="Category"
                required
                value={category}
                onChange={(e) => setCategory(e.target.value)}
              />
            </div>
            <div className="password">
              <input
                type="number"
                placeholder="cost"
                required
                value={cost}
                onChange={(e) => setCost(e.target.value)}
              />
            </div>
            <div className="password">
              <input
                type="number"
                placeholder="Year Of Edition"
                required
                value={yearOfEdition}
                onChange={(e) => setYearOfEdition(e.target.value)}
              />
            </div>
            <div className="password">
              <input
                type="password"
                placeholder="Admin Password"
                required
                value={password}
                onChange={(e) => setPassword(e.target.value)}
              />
            </div>
            <input type="submit" value="Update" className="loginBtn" />
          </form>
        </div>
      )}
    </Fragment>
  );
};
export default UpdateBook;
