import React, { useState, useEffect, Fragment } from "react";
import { useDispatch, useSelector } from "react-redux";
import { useNavigate } from "react-router-dom";
import { toast } from "react-toastify";

import "../Admin/UpdateProfile.css";
import PDFViewer from "../Utils/PDFViewer.js";
import Loader from "../layout/Loader/Loader";
import MisSidebar from "../layout/Loader/MisSidebar";
import { CREATE_STAFF_RESET } from "../../constants/staffConstant";
import { createStaff , clearErrors } from "../../actions/staffActions";


const CreateStaff = () => {
    const dispatch = useDispatch();
   // const navigate = useNavigate();
  
    const { loading, isCreated, error } = useSelector(
      (state) => state.newStaff
    );

    const tomorrow = new Date();
    tomorrow.setDate(tomorrow.getDate() + 1);
    const tomorrowFormatted = tomorrow.toISOString().split('T')[0];
  
    const [name, setName] = useState();
    const [phone, setPhone] = useState();
    const [qualification,setQualification] = useState();
    const [salary,setSalary] = useState();
    const [leavesAllowed,setLeavesAllowed] = useState();
    const [dateOfJoining,setDateOfJoining] = useState();
    const [experience,setExperience] = useState();
    const [designation,setDesignation] = useState();
    const [subject,setSubject] = useState();
    const [password,setPassword] = useState();
    const [images, setImages] = useState([]);
    const [imagesPreview, setImagesPreview] = useState([]);


    const createStaffImagesChange = (e) => {
        const files = Array.from(e.target.files);
    
        setImages([]);
        setImagesPreview([]);
    
        files.forEach((file) => {
          const reader = new FileReader();
          
          reader.readAsDataURL(file);
          reader.onload = () => {
            if (reader.readyState === 2) {
              setImagesPreview((old) => [...old, reader.result]);
              setImages((old) => [...old, reader.result]);
              //console.log("ye chla hai - 1");
            }
          };
        });
      };

    const createSubmit = (e) => {
      e.preventDefault();
      try {
        const data = {
          name,
          phone,
          qualification,
          salary,
          leavesAllowed,
          dateOfJoining,
          experience,
          designation,
          subject,
          images,
          password
        };
        dispatch(createStaff(data));
      } catch (err) {}
    };
  
    const success = () => {
      toast.success("Staff Created Successfully");
    };
    const failure = (error) => {
      toast.error(error);
    };
    useEffect(() => {
      if (error) {
        failure(error);
        dispatch(clearErrors());
      }
  
      if (isCreated) {
        //  alert.success("Profile updated successfully");
        success();
        dispatch({ type: CREATE_STAFF_RESET });
      }
    }, [dispatch, error, isCreated]);
  
    return (
      <Fragment>
        {loading ? (
          <Loader />
        ) : (
          <div className="cardUPR">
            <MisSidebar />
            <p>create new staff</p>
            <form className="loginForm" onSubmit={createSubmit}>
              <div className="loginPhone">
                <input
                  type="text"
                  placeholder="name"
                  required
                  value={name}
                  onChange={(e) => setName(e.target.value)}
                />
              </div>
              <div className="password">
                <input
                  type="text"
                  placeholder="staff Phone Number"
                  required
                  value={phone}
                  onChange={(e) => setPhone(e.target.value)}
                />
              </div>
              <div className="password">
                <input
                  type="text"
                  placeholder="maximum qualification"
                  required
                  value={qualification}
                  onChange={(e) => setQualification(e.target.value)}
                />
              </div>
              <div className="password">
                <input
                  type="number"
                  placeholder="salary"
                  required
                  value={salary}
                  onChange={(e) => setSalary(e.target.value)}
                />
              </div>
              <div className="password">
                <input
                  type="number"
                  placeholder="leavesAllowed"
                  required
                  value={leavesAllowed}
                  onChange={(e) => setLeavesAllowed(e.target.value)}
                />
              </div>
              <div className="password">
                <input
                  type="number"
                  placeholder="eperience in years"
                  required
                  value={experience}
                  onChange={(e) => setExperience(e.target.value)}
                />
              </div>
              <div className="password">
                <input
                  type="text"
                  placeholder="designation"
                  required
                  value={designation}
                  onChange={(e) => setDesignation(e.target.value)}
                />
              </div>
              <div className="password">
                <input
                  type="text"
                  placeholder="subject"
                  required
                  value={subject}
                  onChange={(e) => setSubject(e.target.value)}
                />
              </div>
              <div className="password">
                <label className="checks">
                 <p>Date Of Joining : </p> 
                <input
                  type="date"
                  placeholder="date of joining"
                  required
                  value={dateOfJoining}
                  onChange={(e) => setDateOfJoining(e.target.value)}
                  max={tomorrowFormatted}
                />
                </label>
              </div>
              <div id="createProductFormFile">
                <p className="checks">choose documents to be uploaded : </p>
                <input
                type="file"
                name="avatar"
                accept=".pdf,image/*"
                onChange={createStaffImagesChange}
                multiple
                />
              </div>

              <div id="createProductFormImage">
               {imagesPreview.map((image, index) => (
                <img src = {image} key = {index} alt="Image Preview"/>
               ))}
              </div>
              <div className="password">
                <input
                  type="password"
                  placeholder="admin password"
                  required
                  value={password}
                  onChange={(e) => setPassword(e.target.value)}
                />
              </div>
              <input type="submit" value="Create" className="loginBtn" />
            </form>
          </div>
        )}
      </Fragment>
    );
  };
  export default CreateStaff;