import React, { Fragment, useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import { toast } from "react-toastify";
import "react-js-dialog-box/dist/index.css";

import "../Books/BookDetails.css";
import {
  getSchoolStudentDetails,
  clearErrors,
} from "../../actions/schoolStudentActions";
import FeeSidebar from "../layout/Loader/FeeSidebar";

const SchoolFeeReceipt = () => {
  const dispatch = useDispatch();
  const { id } = useParams();

  const { error, schoolStudent } = useSelector((state) => state.schoolStudentDetails);

  let payment = {};

  try{
  schoolStudent.feePayments.forEach((pay) => {
    if(pay._id == id) {
        payment = pay;
    }
    console.log(pay._id)
  }) 
  }
  catch(err){
       console.log(err)
  }

  const failure = (error) => {
    toast.error(error);
  };

  useEffect(() => {
    if (error) {
      failure(error);
      dispatch(clearErrors());
    }
  }, [dispatch,error,id]);

  return (
    <Fragment>
      <FeeSidebar/>
      <div className="detailsContainer">
        <h2 className="bookName">{`Receipt No: ${payment.receiptNo}`}</h2>
        <div className="details">
          <span className="prefix">Name : </span>
          <span className="value">{schoolStudent.name}</span>
        </div>
        <div className="details">
          <span className="prefix">Roll Number : </span>
          <span className="value">{schoolStudent.rollNo}</span>
        </div>
        <div className="details">
          <span className="prefix">Phone Number : </span>
          <span className="value">{schoolStudent.phone}</span>
        </div>
        <div className="details">
          <span className="prefix">Stream : </span>
          <span className="value">
            {schoolStudent.stream}
          </span>
        </div>
        <div className="details">
          <span className="prefix">Grade : </span>
          <span className="value">
            {schoolStudent.grade}
          </span>
        </div>
        <div className="details">
          <span className="prefix">Hostel : </span>
          <span className="value">{schoolStudent.hostel === true ? "yes" : "no"}</span>
        </div>
        <div className="details">
          <span className="prefix">Amount Paid : </span>
          <span className="value">{payment && payment.amount}</span>
        </div>
                <div className="details">
                  <span className="prefix">Paid By : </span>
                  <span>{payment && payment.paidBy}</span>
                </div>
                <div className="details">
                  <span className="prefix">Paid On : </span>
                  <span>
                    {payment.paidOn && payment.paidOn.substr(0, 10)}
                  </span>
                </div>
                <div className="details">
                  <span className="prefix">Accepted By : </span>
                  <span>
                    {payment && payment.acceptedBy}
                  </span>
                </div>
      </div>
    </Fragment>
  );
};

export default SchoolFeeReceipt;
