import React, { Fragment, useEffect } from "react";
import "./Homepage.css";
import { Link } from "react-router-dom";

import logo from "../../images/collegeLogo.png";
import "./LibraryHome.css"


function Homepage() {
  
  return (
    <div className="container">
    <div className="libContainer">
      <p className="heading">Modern College management Site</p>
      <img src = {logo} />
      <div>
        <p>Instructions</p>
        <div>
          <p className = "inst"><span className="instSpan">1. </span>Try that every field where text is to be filled should have capital letters (except passwords)</p>
          <p className = "inst"><span className="instSpan">2. </span>Correct spellings must be entered</p>
          <p className = "inst"><span className="instSpan">3. </span>Before entering any student into a degree/stream the fee structure for that degree/stream must be present</p>
        </div>
      </div>
    </div>
    </div>
  );
}

export default Homepage;
