import React, { useState, useEffect, Fragment } from "react";
import { useDispatch, useSelector } from "react-redux";
import { useNavigate } from "react-router-dom";
import { toast } from "react-toastify";

import "../Admin/UpdateProfile.css";
import Loader from "../layout/Loader/Loader";
import InventorySidebar from "../layout/Loader/InventorySidebar";
import {clearErrors, createItem} from "../../actions/inventoryActions";
import { CREATE_ITEM_RESET } from "../../constants/inventoryConstant";

const CreateItem = () => {
  const dispatch = useDispatch();
 // const navigate = useNavigate();

  const { loading, isCreated, error } = useSelector(
    (state) => state.newItem
  );

  const strDate = new Date();
  const tomorrow = new Date();
  tomorrow.setDate(tomorrow.getDate() + 1);
  const tomorrowFormatted = tomorrow.toISOString().split('T')[0];

  const [name, setName] = useState();
  const [lab, setLab] = useState();
  const [cost, setCost] = useState();
  const [stock, setStock] = useState();
  const [damaged,setDamaged] = useState();
  const [dateOfPurchase,setDateOfPurchase] = useState(strDate);
  const [password,setPassword] = useState();

  const labs = [
    "BOTANY",
    "ZOOLOGY",
    "PHYSICS",
    "CHEMISTRY",
    "IT",
    "MESS",
    "INFRASTRUCTURE"
  ]

  const createSubmit = (e) => {
    e.preventDefault();
    try {
      const data = {
        name,
        lab,
        cost,
        stock,
        password,
        damaged,
        dateOfPurchase
      };
      dispatch(createItem(data));
    } catch (err) {}
  };

  const success = () => {
    toast.success("Item Created Successfully");
  };
  const failure = (error) => {
    toast.error(error);
  };
  useEffect(() => {
    if (error) {
      failure(error);
      dispatch(clearErrors());
    }

    if (isCreated) {
      //  alert.success("Profile updated successfully");
      success();
      dispatch({ type: CREATE_ITEM_RESET });
    }
  }, [dispatch, error, isCreated]);

  return (
    <Fragment>
      {loading ? (
        <Loader />
      ) : (
        <div className="cardUPR">
          <InventorySidebar />
          <p>create new item</p>
          <form className="loginForm" onSubmit={createSubmit}>
            <div className="loginPhone">
              <input
                type="text"
                placeholder="name"
                required
                value={name}
                onChange={(e) => setName(e.target.value)}
              />
            </div>
            <div className="password">
              <input
                type="number"
                placeholder="cost"
                required
                value={cost}
                onChange={(e) => setCost(e.target.value)}
              />
            </div>
            <div className="password">
              <input
                type="number"
                placeholder="initial stock"
                required
                value={stock}
                onChange={(e) => setStock(e.target.value)}
              />
            </div>
            <div className="password">
              <input
                type="number"
                placeholder="damaged"
                required
                value={damaged}
                onChange={(e) => setDamaged(e.target.value)}
              />
            </div>
            <div>
              <select onChange={(e) => setLab(e.target.value)}>
                <option value="">{lab ? lab : "Select Lab"}</option>
                {labs.map((cate) => (
                  <option key={cate} value={cate}>
                    {cate}
                  </option>
                ))}
              </select>
            </div>
            <div className="password">
                <label className="checks">
                 <p>Date Of Purchase : </p> 
                <input
                  type="date"
                  placeholder="date of purchase"
                  required
                  value={dateOfPurchase}
                  onChange={(e) => setDateOfPurchase(e.target.value)}
                  max={tomorrowFormatted}
                />
                </label>
            </div>
            <div className="password">
              <input
                type="password"
                placeholder="password"
                required
                value={password}
                onChange={(e) => setPassword(e.target.value)}
              />
            </div>
            <input type="submit" value="Create" className="loginBtn" />
          </form>
        </div>
      )}
    </Fragment>
  );
};
export default CreateItem;
