import React, { useState } from "react";
import { Document, Page, pdfjs } from "react-pdf";

// Set the worker source for PDF.js
pdfjs.GlobalWorkerOptions.workerSrc = new URL(
  "pdfjs-dist/build/pdf.worker.min.js",
  import.meta.url
).toString();

// Define the PDF viewer component
function PDFViewer({ file }) {
  // Use state hooks to store the number of pages and the current page
  const [numPages, setNumPages] = useState(null);
  const [pageNumber, setPageNumber] = useState(1);

  // Define a callback function to update the number of pages
  function onDocumentLoadSuccess({ numPages }) {
    setNumPages(numPages);
  }

  // Define a function to go to the previous page
  function goToPrevPage() {
    if (pageNumber > 1) {
      setPageNumber((prevPageNumber) => prevPageNumber - 1);
    }
  }

  // Define a function to go to the next page
  function goToNextPage() {
    if (pageNumber < numPages) {
      setPageNumber((prevPageNumber) => prevPageNumber + 1);
    }
  }

  // Return the JSX element for the PDF viewer
  return (
    <div className="pdf-viewer">
      <Document file={file} onLoadSuccess={onDocumentLoadSuccess}>
        <Page pageNumber={pageNumber} />
      </Document>
      <div className="controls">
        <button onClick={goToPrevPage}>Prev</button>
        <span>
          Page {pageNumber} of {numPages}
        </span>
        <button onClick={goToNextPage}>Next</button>
      </div>
    </div>
  );
}

export default PDFViewer;