import React, { useState, useEffect, Fragment } from "react";
import { useDispatch, useSelector } from "react-redux";
import {
  login,
  clearErrors,
  updatePhone,
  loadAdmin,
  updatePassword,
} from "../../actions/adminActions";
import { useNavigate } from "react-router-dom";
import {toast} from "react-toastify";

import "./UpdateProfile.css";
import Loader from "../layout/Loader/Loader";
import adminImage from "../../images/adminImage.png";
import { UPDATE_PASSWORD_RESET } from "../../constants/adminConstant";

const UpdatePassword = () => {
  const dispatch = useDispatch();
  const navigate = useNavigate();

  const [oldPassword, setOldPassword] = useState();
  const [newPassword,setNewPassword] = useState();
  const [confirmPassword,setConfirmPassword] = useState();

  const {loading,isUpdated,error} = useSelector(state => state.adminProfile);

  const updateSubmit = (e) => {
    e.preventDefault();
    const data = {
      oldPassword,
      newPassword,
      confirmPassword
    };
    dispatch(updatePassword(data));
  };


  const success = () => {
    toast.success("Admin Password Updated Successfully");
  };
  const failure = (error) => {
    toast.error(error);
  };

  useEffect(() => {
    if (error) {
      failure(error);
      dispatch(clearErrors());
    }

    if (isUpdated) {
      //  alert.success("Profile updated successfully");
      success();
      navigate("/");
      dispatch({ type: UPDATE_PASSWORD_RESET });
    }
  }, [dispatch, error, isUpdated]);
  return (
    <Fragment>
      {loading ? (
        <Loader />
      ) : (
        <div className="cardUPR">
          <p>Update Password</p>
          <img src={adminImage} />
          <form className="loginForm" onSubmit={updateSubmit}>
            <div className="loginPhone">
              <input
                type="password"
                placeholder="Current Password"
                required
                value={oldPassword}
                onChange={(e) => setOldPassword(e.target.value)}
              />
            </div>
            <div className="name">
              <input
                type="password"
                placeholder="New Password"
                required
                value={newPassword}
                onChange={(e) => setNewPassword(e.target.value)}
              />
            </div>
            <div className="password">
              <input
                type="password"
                placeholder="Confirm Password"
                required
                value={confirmPassword}
                onChange={(e) => setConfirmPassword(e.target.value)}
              />
            </div>
            <input type="submit" value="Update" className="loginBtn" />
          </form>
        </div>
      )}
    </Fragment>
  );
};
export default UpdatePassword;
