import React, { Fragment, useEffect, useState } from "react";
import { useSelector, useDispatch } from "react-redux";
import DataTable from "react-data-table-component";
import { ReactDialogBox } from "react-js-dialog-box";
import "react-js-dialog-box/dist/index.css";

import "../Books/SearchBook.css";
import {
  clearErrors,
  getAllUsers,
  deleteUser,
} from "../../actions/usersActions";
import { toast } from "react-toastify";
import { DELETE_USER_RESET } from "../../constants/usersConstant";
import LibrarySidebar from "../layout/Loader/LibrarySidebar";

const SearchUsers = () => {
  const dispatch = useDispatch();

  const { error, users } = useSelector((state) => state.allUsers);

  const { error: deleteError, isDeleted } = useSelector(
    (state) => state.userProfile
  );

  const [isOpen, setIsOpen] = useState(false);
  const [password, setPassword] = useState();
  const [id, setId] = useState();
  const [category, setCategory] = useState();
  const [value, setValue] = useState();

  let issuedSort = 0;
  let nameSort = 0;
  let returnedSort = 0;
  let yetToReturnSort = 0;

  const categories = ["name", "phone"];

  const openDialog = () => {
    setIsOpen(true);
  };

  const closeDialog = () => {
    setPassword();
    setIsOpen(false);
  };

  const makeId = (id) => {
    setId(id);
  };

  const deleteSubmit = () => {
    const temp = password;
    closeDialog();
    dispatch(deleteUser(id, temp));
  };

  const successDelete = () => {
    toast.success("User Deleted Successfully");
  };
  const failure = (error) => {
    toast.error(error);
  };

  const sortFunction = (type) => {
    if (type === "issued") {
      issuedSort++;
      if (issuedSort % 2 === 0) users.sort((a, b) => b.issues - a.issues);
      else users.sort((a, b) => a.issues - b.issues);
    }
    
    else if (type === "name") {
      nameSort++;
      if (nameSort % 2 === 0)
        users.sort((a, b) => b.name.localeCompare(a.name));
      else users.sort((a, b) => a.name.localeCompare(b.name));
    }
    
    else if (type === "returned") {
      returnedSort++;
      if (returnedSort % 2 === 0) users.sort((a, b) => b.returns - a.returns);
      else users.sort((a, b) => a.returns - b.returns);
    }
    
    else if (type === "yetToReturn") {
      yetToReturnSort++;
      if (yetToReturnSort % 2 === 0)
        users.sort((a, b) => (b.issues - b.returns) - (a.issues - a.returns));
      else users.sort((a, b) => (a.issues - a.returns) - (b.issues - b.returns));
    }
  };

  {users && users.forEach((user,index) => {
    user.num = index + 1    
  })}

   

  const columns = [
    {
      name: (
        <h3 className="headings">
          Serial No.
        </h3>
      ),
      selector: (row) => <span className="cellData">{row.num}</span>,
    },
    {
      name: (
        <h3 className="headings" onClick={() => sortFunction("name")}>
          Name
        </h3>
      ),
      selector: (row) => (
        <a className="cellLink" href={`/user/${row._id}`}>
          <p>{row.name}</p>
        </a>
      ),
      sortable: true,
      wrap: true,
    },
    {
      name: <h3 className="headings">Phone</h3>,
      selector: (row) => <span className="cellData">{row.phone}</span>,
      wrap: true,
    },

    {
      name: (
        <h4 className="headings" onClick={() => sortFunction("issued")}>
          issued
        </h4>
      ),
      selector: (row) => <span className="cellData">{row.issues}</span>,
      sortable: true,
      wrap: true,
    },
    {
      name: (
        <h4 className="headings" onClick={() => sortFunction("returned")}>
          returned
        </h4>
      ),
      selector: (row) => <span className="cellData">{row.returns}</span>,
      sortable: true,
      wrap: true,
    },
    {
      name: (
        <h4 className="headings" onClick={() => sortFunction("yetToReturn")}>
          yetToReturn
        </h4>
      ),
      selector: (row) => <span className="cellData">{row.issues - row.returns}</span>,
      sortable: true,
      wrap: true,
    },
    {
      name: "",
      cell: (row) => (
        <a className="rlinks" href={`/update/user/${row._id}`}>
          <button className="btn">Edit</button>
        </a>
      ),
    },
    {
      name: "",
      cell: (row) => (
        <button
          onClick={() => {
            openDialog();
            makeId(row._id);
          }}
          className="delBtn"
        >
          Delete
        </button>
      ),
    },
  ];

  const searchSubmit = (e) => {
    e.preventDefault();
    dispatch(getAllUsers(category, value));
  };

  useEffect(() => {
    if (error) {
      failure(error);
      dispatch(clearErrors());
    }

    if (deleteError) {
      failure(deleteError);
      dispatch(clearErrors());
    }

    if (isDeleted === true) {
      successDelete();
      dispatch({ type: DELETE_USER_RESET });
    }

    dispatch(getAllUsers());
  }, [dispatch, error, deleteError, isDeleted]);

  return (
    <Fragment>
      {isOpen && (
        <ReactDialogBox
          closeBox={closeDialog}
          modalWidth="28%"
          headerBackgroundColor="rgb(192, 188, 188)"
          headerTextColor="rgb(39, 39, 39)"
          headerHeight="50"
          closeButtonColor="#D92736"
          bodyBackgroundColor="white"
          bodyTextColor="black"
          bodyHeight="35%"
          headerText="Enter Admin Password"
        >
          <div>
            <form className="loginForm" onSubmit={deleteSubmit}>
              <div className="password">
                <input
                  type="password"
                  placeholder="password"
                  value={password}
                  onChange={(e) => setPassword(e.target.value)}
                ></input>
              </div>
              <button type="submit" className="loginBtn">
                delete
              </button>
            </form>
          </div>
        </ReactDialogBox>
      )}
      <LibrarySidebar/>
      <div className="dashboard">
        <div className="productListContainer">
          <h1 id="productListHeading">Modern College Library Users</h1>
          <div className="searchBox">
            <select
              onChange={(e) => {
                setValue("");
                setCategory(e.target.value);
              }}
              className="searchType"
            >
              <option value="">Select</option>
              {categories.map((cate) => (
                <option key={cate} value={cate}>
                  {cate}
                </option>
              ))}
            </select>
            <div>
              <form className="searchForm" onSubmit={searchSubmit}>
                <input
                  type="text"
                  className="searchInput"
                  value={value}
                  onChange={(e) => {
                    setValue(e.target.value);
                  }}
                />
                <button type="submit" className="searchBtn">
                  Search
                </button>
              </form>
            </div>
          </div>
          <DataTable
            pagination
            columns={columns}
            data={users}
            className="productListTable"
            highlightOnHover={true}
            persistTableHead={true}
            fixedHeader={true}
          />
        </div>
      </div>
    </Fragment>
  );
};

export default SearchUsers;
