import {
  LOAD_ADMIN_FAIL,
  LOAD_ADMIN_REQUEST,
  LOAD_ADMIN_SUCCESS,
  LOGIN_FAIL,
  LOGIN_REQUEST,
  LOGIN_SUCCESS,
  LOGOUT_FAIL,
  LOGOUT_SUCCESS,
  UPDATE_PASSWORD_FAIL,
  UPDATE_PASSWORD_REQUEST,
  UPDATE_PASSWORD_RESET,
  UPDATE_PASSWORD_SUCCESS,
  UPDATE_PHONE_FAIL,
  UPDATE_PHONE_REQUEST,
  UPDATE_PHONE_RESET,
  UPDATE_PHONE_SUCCESS,
  CLEAR_ERRORS,
} from "../constants/adminConstant";
import axios from "axios";

//login
export const login = (phone,password) => async(dispatch) => {
    try{
          dispatch({type:LOGIN_REQUEST});
          const config = {headers: {"Content-Type":"application/json"}};
          const { data } = await axios.post(
            `/api/v1/login`,
            { phone, password },
            config
          );
          dispatch({type:LOGIN_SUCCESS,payload: data.admin});
    }
    catch(error){
        dispatch({ type: LOGIN_FAIL, payload: error.response.data.message });
    } 
}

// LOAD USER

export const loadAdmin = () => async (dispatch) => {
    try {
      dispatch({ type: LOAD_ADMIN_REQUEST });
      const { data } = await axios.get(`/api/v1/me`);
  
      dispatch({ type: LOAD_ADMIN_SUCCESS, payload: data.admin });
    } catch (error) {
      dispatch({ type: LOAD_ADMIN_FAIL, payload: error.response.data.message });
    }
  };

   // to log out
  export const logout = () =>async (dispatch)=>{
        try{
                 await axios.get(`/api/v1/logout`);
                 dispatch({type:LOGOUT_SUCCESS})
        }
        catch(error){
           dispatch({type:LOGOUT_FAIL,
          payload:error.response.data.message
        })
        }
  }


    // to update
  export const updatePhone = (userData) => async (dispatch) => {
    try {
      dispatch({ type: UPDATE_PHONE_REQUEST });
  
      const config = { headers: { "Content-Type": "application/json" } };
  
      const { data } = await axios.put(`/api/v1/update/phone`, userData, config);
  
      dispatch({ type: UPDATE_PHONE_SUCCESS, payload: data.success });
    } catch (error) {
      dispatch({
        type: UPDATE_PHONE_FAIL,
        payload: error.response.data.message,
      });
    }
  };


  // update password
  export const updatePassword = (passwords) => async (dispatch) => {
    try {
      dispatch({ type: UPDATE_PASSWORD_REQUEST });
  
      const config = { headers: { "Content-Type": "application/json" } };
  
      const { data } = await axios.put(
        `/api/v1/update/password`,
        passwords,
        config
      );
  
      dispatch({ type: UPDATE_PASSWORD_SUCCESS, payload: data.success });
    } catch (error) {
      dispatch({
        type: UPDATE_PASSWORD_FAIL,
        payload: error.response.data.message,
      });
    }
  };

  // to clear errors
export const clearErrors = () => async(dispatch) => {
    dispatch({
       type: CLEAR_ERRORS
    });
};