import React, { useState, useEffect, Fragment } from "react";
import { useDispatch, useSelector } from "react-redux";
import { login, clearErrors, updatePhone, loadAdmin, logout } from "../../actions/adminActions";
import { Link, useNavigate } from "react-router-dom";
import {  toast } from "react-toastify";

import "./UpdateProfile.css";
import Loader from "../layout/Loader/Loader";
import adminImage from "../../images/adminImage.png"
import { UPDATE_PHONE_RESET } from "../../constants/adminConstant";

const UpdateProfile = () => {
  const dispatch = useDispatch();
  const navigate = useNavigate();

  const { loading, admin,isAuthenticated } = useSelector((state) => state.admin);
  const {loading:updateLoading , error,isUpdated} = useSelector(state => state.adminProfile)
  const Admin = Array.isArray(admin) ? admin[0] : admin;
  console.log(Admin);
  const [phone, setPhone] = useState();
  const [name, setName] = useState();
  const [password,setPassword] = useState();

  const logoutFunc = () =>{
    dispatch(logout());
  }

  const updateSubmit = (e) => {
    e.preventDefault();
    try{
    const data = {
      phone,
      name,
      password
    };
    dispatch(updatePhone(data));
  }
  catch(err){

  }
  };

  const success = () =>{
    toast.success("Admin Profile Updated Successfully")
  }
  const failure = (error) =>{
    toast.error(error);
  }
  useEffect(() => {
    if (error) {
      failure(error);
      dispatch(clearErrors());
    }

    if (isUpdated) {
    //  alert.success("Profile updated successfully");
      success();
      dispatch(loadAdmin());
      navigate("/");
      dispatch({ type: UPDATE_PHONE_RESET });
    }
  }, [dispatch, error,Admin,isUpdated,isAuthenticated]);

  return (
    <Fragment>
      {(loading || updateLoading)? (
        <Loader />
      ) : (
        <div className="cardUPR">
          <p>Update Phone and Name</p>
          <img src={adminImage} />
          <form className="loginForm" onSubmit={updateSubmit}>
            <div className="loginPhone">
              <input
                type="text"
                placeholder="phone"
                required
                value={phone ? phone : Admin.phone}
                onChange={(e) => setPhone(e.target.value)}
              />
            </div>
            <div className="name">
              <input
                type="text"
                placeholder="Name"
                required
                value={name ? name : Admin.name}
                onChange={(e) => setName(e.target.value)}
              />
            </div>
            <div className="password">
              <input
                type="password"
                placeholder="Password"
                required
                value={password}
                onChange={(e) => setPassword(e.target.value)}
              />
            </div>
            <input type="submit" value="Update" className="loginBtn" />
          </form>
          <Link to = "/update/password" className="updatePss">
            Update Password
          </Link>
          <button className="remBtn" onClick = {logoutFunc}>
            Logout
          </button>
        </div>
      )}
    </Fragment>
  );
};
export default UpdateProfile;
