import { createStore,combineReducers, applyMiddleware } from "redux";
import thunk from "redux-thunk";
import { composeWithDevTools } from "redux-devtools-extension";import { allBooksReducer, bookDetailsReducer, bookProfileReducer, createBookReducer } from "./reducers/booksReducer";
import { allUsersReducer, createUserReducer, userDetailsReducer, userProfileReducer } from "./reducers/usersReducer";
import { adminProfileReducer, adminReducer } from "./reducers/adminReducer";
import { allFeesReducer, createFeeReducer, feeDetailsReducer, feeProfileReducer } from "./reducers/feeReducer";
import { allCollegeStudentsReducer, collegeStudentDetailsReducer, collegeStudentProfileReducer, createCollegeStudentReducer } from "./reducers/collegeStudentReducer";
import { allSchoolStudentsReducer, createScoolStudentReducer, schoolStudentDetailsReducer, schoolStudentProfileReducer } from "./reducers/schoolStudentReducer";
import { allItemsReducer, createItemReducer, itemDetailsReducer, itemProfileReducer } from "./reducers/inventoryReducer";
import { allStaffsReducer, createStaffReducer, staffDetailsReducer, staffProfileReducer } from "./reducers/staffReducer";

const reducer = combineReducers({
  allBooks: allBooksReducer,
  bookDetails: bookDetailsReducer,
  newBook: createBookReducer,
  bookProfile:bookProfileReducer,
  allUsers:allUsersReducer,
  newUser: createUserReducer,
  userProfile:userProfileReducer,
  userDetails:userDetailsReducer,
  admin:adminReducer,
  adminProfile:adminProfileReducer,
  allFees: allFeesReducer,
  feeDetails:feeDetailsReducer,
  newFee: createFeeReducer,
  feeProfile: feeProfileReducer,
  allCollegeStudents: allCollegeStudentsReducer,
  collegeStudentDetails : collegeStudentDetailsReducer,
  newCollegeStudent: createCollegeStudentReducer,
  collegeStudentProfile: collegeStudentProfileReducer,
  allSchoolStudents: allSchoolStudentsReducer,
  schoolStudentDetails: schoolStudentDetailsReducer,
  newSchoolStudent : createScoolStudentReducer,
  schoolStudentProfile: schoolStudentProfileReducer,
  allItems:allItemsReducer,
  itemDetails:itemDetailsReducer,
  newItem:createItemReducer,
  itemProfile:itemProfileReducer,
  allStaff:allStaffsReducer,
  staffDetails:staffDetailsReducer,
  newStaff:createStaffReducer,
  staffProfile:staffProfileReducer
});

let initialState = {
 
};
const middleware = [thunk];
const store = createStore(
  reducer,
  initialState,
  composeWithDevTools(applyMiddleware(...middleware))
);

export default store;
