import React, { Fragment, useEffect, useState } from "react";
import { useSelector, useDispatch } from "react-redux";
import DataTable from "react-data-table-component";
import { ReactDialogBox } from "react-js-dialog-box";
import "react-js-dialog-box/dist/index.css";

import "../Books/SearchBook.css";
import Loader from "../layout/Loader/Loader";
import {
  clearErrors,
  getAllFees,
  deleteFee
} from "../../actions/feeActions";
import { toast } from "react-toastify";
import { DELETE_FEE_RESET } from "../../constants/feeConstant";
import FeeSidebar from "../layout/Loader/FeeSidebar";

const SearchFee = () => {
  const dispatch = useDispatch();

  const { error, fees,loading } = useSelector((state) => state.allFees);

  const { error: deleteError, isDeleted } = useSelector(
    (state) => state.feeProfile
  );

  const [isOpen, setIsOpen] = useState(false);
  const [password, setPassword] = useState();
  const [id, setId] = useState();
  const [value, setValue] = useState(); 
  const [option,setOption] = useState();
  
  
  let amountSort = 0;
  let gradeSort = 0;
  let semesterSort = 0;

  const options = ["degree", "stream","hostel","semester","grade"];
  
  const openDialog = () => {
    setIsOpen(true);
  };

  const closeDialog = () => {
    setPassword();
    setIsOpen(false);
  };

  const makeId = (id) => {
    setId(id);
  };

  const deleteSubmit = () => {
    const temp = password;
    closeDialog();
    dispatch(deleteFee(id, temp));
  };

  const successDelete = () => {
    toast.success("Fee record Deleted Successfully");
  };
  const failure = (error) => {
    toast.error(error);
  };


  const sortFunction = (type) => {
    if (type === "amount") {
      amountSort++;
      if (amountSort % 2 === 0) fees.sort((a, b) => b.amount - a.amount);
      else fees.sort((a, b) => a.amount - b.amount);
    } 
    
    else if (type === "grade") {
      gradeSort++;
      if (gradeSort % 2 === 0)
        fees.sort((a, b) => b.grade - a.grade);
      else fees.sort((a, b) => a.grade - b.grade);
    }

    else if (type === "semester") {
        semesterSort++;
        if (semesterSort % 2 === 0)
          fees.sort((a, b) => b.semester - a.semester);
        else fees.sort((a, b) => a.semester - b.semester);
      }
  

  };

  {fees && fees.forEach((fee,index) => {
    fee.num = index + 1    
  })}

   

  const columns = [
    {
      name: (
        <h3 className="headings">
          Serial No.
        </h3>
      ),
      selector: (row) => <span className="cellData">{row.num}</span>,
    },
    {
      name: (
        <h3 className="headings" >
          Degree
        </h3>
      ),
      selector: (row) => <span className="cellData">{row.degree}</span>,
      sortable: true,
      wrap: true,
    },
    {
      name: <h4 className="headings">Stream</h4>,
      selector: (row) => (
        <span
          className="cellData"
        >
          {row.stream}
        </span>
      ),
      wrap: true,
    },
    {
      name: <h4 className="headings" onClick={()=>sortFunction("semester")}>semester</h4>,
      selector: (row) => <span className="cellData">{row.semester}</span>,
      wrap: true,
      sortable:true,
    },
    {
      name: <h4 className="headings" onClick={()=>sortFunction("grade") }>grade</h4>,
      selector: (row) => <span className="cellData">{row.grade}</span>,
      wrap: true,
      sortable:true,
    },
    {
        name: <h4 className="headings">Hostel</h4>,
        selector: (row) => (
          <span
            className="cellData"
          >
            {row.hostel === true ? "yes" : "no"}
          </span>
        ),
        wrap: true,
      },
    {
        name: <h4 className="headings" onClick={()=>sortFunction("amount") }>Amount</h4>,
        selector: (row) => (
          <span
            className="cellData"
          >
            {row.amount}
          </span>
        ),
        wrap: true,
        sortable:true,
      },  
    {
      name: "",
      cell: (row) => (
        <a className="rlinks" href={`/update/fee/${row._id}`}>
          <button className="btn">Edit</button>
        </a>
      ),
    },
    {
      name: "",
      cell: (row) => (
        <button
          onClick={() => {
            openDialog();
            makeId(row._id);
          }}
          className="delBtn"
        >
          Delete
        </button>
      ),
    },
  ]; 

  
  const searchSubmit = (e) => {
    e.preventDefault();
    dispatch(getAllFees(option, value));
  };


  useEffect(() => {
    if (error) {
      failure(error);
      dispatch(clearErrors());
    }

    if (deleteError) {
      failure(deleteError);
      dispatch(clearErrors());
    }

    if (isDeleted === true) {
      successDelete();
      dispatch({ type: DELETE_FEE_RESET });
    }

    dispatch(getAllFees());
  }, [dispatch, error, deleteError, isDeleted]);


  return (
    <Fragment>
     
      {isOpen && (
        <ReactDialogBox
          closeBox={closeDialog}
          modalWidth="28%"
          headerBackgroundColor="rgb(192, 188, 188)"
          headerTextColor="rgb(39, 39, 39)"
          headerHeight="50"
          closeButtonColor="#D92736"
          bodyBackgroundColor="white"
          bodyTextColor="black"
          bodyHeight="35%"
          headerText="Enter Admin Password"
        >
          <div>
            <form className="loginForm" onSubmit={deleteSubmit}>
              <div className="password">
                <input
                  type="password"
                  placeholder="password"
                  value={password}
                  onChange={(e) => setPassword(e.target.value)}
                ></input>
              </div>
              <button type="submit" className="loginBtn">
                delete
              </button>
            </form>
          </div>
        </ReactDialogBox>
      )}
      <FeeSidebar/>
      <div className="dashboard">
        <div className="productListContainer">
          <h1 id="productListHeading">Modern College fee structures</h1>
          <div className="searchBox">
            <select
              onChange={(e) => {
                setValue("");
                setOption(e.target.value);
              }}
              className="searchType"
            >
              <option value="">Select</option>
              {options.map((cate) => (
                <option key={cate} value={cate}>
                  {cate}
                </option>
              ))}
            </select>
            <div>
              <form className="searchForm" onSubmit={searchSubmit}>
                <input
                  type="text"
                  className="searchInput"
                  value={value}
                  onChange={(e) => {
                    setValue(e.target.value);
                  }}
                />
                <button type="submit" className="searchBtn">
                  Search
                </button>
              </form>
            </div>
          </div>
          <DataTable
            pagination
            columns={columns}
            data={fees}
            className="productListTable"
            highlightOnHover={true}
            persistTableHead={true}
            fixedHeader={true}
          />
        </div>
      </div>
    </Fragment>
  );
};

export default SearchFee;
