import React, { useState, useEffect, Fragment } from "react";
import { useDispatch, useSelector } from "react-redux";
import { useNavigate, useParams } from "react-router-dom";
import { toast } from "react-toastify";

import {
  clearErrors,
  getFeeDetails,
  updateFee
} from "../../actions/feeActions";
import "../Admin/UpdateProfile.css";
import Loader from "../layout/Loader/Loader";
import { UPDATE_FEE_RESET } from "../../constants/feeConstant";
import FeeSidebar from "../layout/Loader/FeeSidebar";

const UpdateFee = () => {
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const { id } = useParams();

  const { loading, isUpdated, error } = useSelector(
    (state) => state.feeProfile
  );
  const {
    loading: feeLoading,
    error: feeError,
    fee,
  } = useSelector((state) => state.feeDetails);

  const [amount, setAmount] = useState();
  const [password,setPassword] = useState();
  const [spPassword,setSpPassword] = useState();

  const updateSubmit = (e) => {
    e.preventDefault();
    try {
          const data = {
           amount,
           password,
           spPassword
          };
      dispatch(updateFee(id, data));
    } catch (err) {}
  };

  const success = () => {
    toast.success("Fee Updated Successfully");
  };
  const failure = (error) => {
    toast.error(error);
  };
  useEffect(() => {
    if (!fee || (fee && fee._id !== id)) dispatch(getFeeDetails(id));
    else {
      setAmount(fee.amount)
    }
    if (error) {
      failure(error);
      dispatch(clearErrors());
    }
    if(feeError){
        failure(feeError);
        dispatch(clearErrors());
    }
    if (isUpdated) {
      //  alert.success("Profile updated successfully");
      success();
      navigate(`/fee/${fee._id}`);
      dispatch({ type: UPDATE_FEE_RESET });
    }
  }, [dispatch, error, isUpdated, id,fee]);

  return (
    <Fragment>
      {loading || feeLoading ? (
        <Loader />
      ) : (
        <div className="cardUPR">
          <FeeSidebar/>
          <p>Update Fee Amount</p>
          <form className="loginForm" onSubmit={updateSubmit}>
            <div className="loginPhone">
              <input
                type="number"
                placeholder="amount"
                required
                value={amount}
                onChange={(e) => setAmount(e.target.value)}
              />
            </div>
            <div className="password">
              <input
                type="password"
                placeholder="admin password"
                required
                value={password}
                onChange={(e) => setPassword(e.target.value)}
              />
            </div>
            <div className="password">
              <input
                type="password"
                placeholder="special password"
                required
                value={spPassword}
                onChange={(e) => setSpPassword(e.target.value)}
              />
            </div>
            <input type="submit" value="Update" className="loginBtn" />
          </form>
        </div>
      )}
    </Fragment>
  );
};
export default UpdateFee;
