import {
  CREATE_BOOK_FAIL,
  CREATE_BOOK_REQUEST,
  CREATE_BOOK_SUCCESS,
  UPDATE_BOOK_FAIL,
  UPDATE_BOOK_REQUEST,
  UPDATE_BOOK_RESET,
  UPDATE_BOOK_SUCCESS,
  ALL_BOOKS_FAIL,
  ALL_BOOKS_REQUEST,
  ALL_BOOKS_SUCCESS,
  BOOK_DETAILS_FAIL,
  BOOK_DETAILS_REQUEST,
  BOOK_DETAILS_SUCCESS,
  WITHDRAW_BOOK_FAIL,
  WITHDRAW_BOOK_REQUEST,
  WITHDRAW_BOOK_RESET,
  WITHDRAW_BOOK_SUCCESS,
  RETURN_BOOK_FAIL,
  RETURN_BOOK_REQUEST,
  RETURN_BOOK_RESET,
  RETURN_BOOK_SUCCESS,
  DELETE_BOOK_FAIL,
  DELETE_BOOK_REQUEST,
  DELETE_BOOK_RESET,
  DELETE_BOOK_SUCCESS,
  CLEAR_ERRORS,
  CREATE_BOOK_RESET,
} from "../constants/booksConstant";

export const createBookReducer = (state = {book:{}}, action) => {
  switch (action.type) {
    case CREATE_BOOK_REQUEST:
      return {
        loading: true,
        isCreated: false,
      };
    case CREATE_BOOK_SUCCESS:
      return {
        ...state,
        loading: false,
        isCreated: action.payload.success,
        book:action.payload.book
      };
    case CREATE_BOOK_FAIL:
      return {
        ...state,
        loading: false,
        error: action.payload,
      };
    case CREATE_BOOK_RESET:
      return {
        ...state,
        isCreated: false,
      };  
    case CLEAR_ERRORS:
      return {
        ...state,
        error: null,
      };
    default:
      return state;
  }
};

export const bookProfileReducer = (state = {}, action) => {
  switch (action.type) {
    case UPDATE_BOOK_REQUEST:
    case DELETE_BOOK_REQUEST:
    case WITHDRAW_BOOK_REQUEST:
    case RETURN_BOOK_REQUEST:
      return {
        ...state,
        loading: true,
      };
    case UPDATE_BOOK_SUCCESS:
      return {
        ...state,
        loading: false,
        isUpdated: action.payload,
      };

    case DELETE_BOOK_SUCCESS:
      return {
        ...state,
        loading: false,
        isDeleted: action.payload.success,
        message: action.payload.message,
      };
    case WITHDRAW_BOOK_SUCCESS:
      return {
        ...state,
        loading: false,
        isWithdrawn: action.payload,
      };
    case RETURN_BOOK_SUCCESS:
      return {
        ...state,
        loading: false,
        isReturned: action.payload,
      };
    case UPDATE_BOOK_FAIL:
    case DELETE_BOOK_FAIL:
    case WITHDRAW_BOOK_FAIL:
    case RETURN_BOOK_FAIL:
      return {
        ...state,
        loading: false,
        error: action.payload,
      };

    case UPDATE_BOOK_RESET:
      return {
        ...state,
        isUpdated: false,
      };

    case DELETE_BOOK_RESET:
      return {
        ...state,
        isDeleted: false,
      };
    case WITHDRAW_BOOK_RESET:
      return {
        ...state,
        isWithdrawn: false,
      };
    case RETURN_BOOK_RESET:
      return {
        ...state,
        isReturned: false,
      };
    case CLEAR_ERRORS:
      return {
        ...state,
        error: null,
      };

    default:
      return state;
  }
};

export const allBooksReducer = (state = { books: [] }, action) => {
  switch (action.type) {
    case ALL_BOOKS_REQUEST:
      return {
        ...state,
        loading: true,
      };
    case ALL_BOOKS_SUCCESS:
      return {
        ...state,
        loading: false,
        books: action.payload.books,
        booksCount: action.payload.booksCount,
        resultPerPage: action.payload.resultPerPage
      };

    case ALL_BOOKS_FAIL:
      return {
        ...state,
        loading: false,
        error: action.payload,
      };

    case CLEAR_ERRORS:
      return {
        ...state,
        error: null,
      };

    default:
      return state;
  }
};

export const bookDetailsReducer = (state = { book: {} }, action) => {
  switch (action.type) {
    case BOOK_DETAILS_REQUEST:
      return {
        ...state,
        loading: true,
      };
    case BOOK_DETAILS_SUCCESS:
      return {
        ...state,
        loading: false,
        book: action.payload,
      };

    case BOOK_DETAILS_FAIL:
      return {
        ...state,
        loading: false,
        error: action.payload,
      };

    case CLEAR_ERRORS:
      return {
        ...state,
        error: null,
      };

    default:
      return state;
  }
};
