import React, { Fragment, useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import { toast } from "react-toastify";
import { ReactDialogBox } from "react-js-dialog-box";
import "react-js-dialog-box/dist/index.css";
import {Link} from "react-router-dom"

import "../Books/BookDetails.css";
import {
  getCollegeStudentDetails,
  clearErrors,
  payFeesCollege,
  updateSemester
} from "../../actions/collegeStudentActions";
import { PAYMENT_CSTUDENT_RESET,UPDATE_SEM_RESET } from "../../constants/collegeStudentConstant";
import FeeSidebar from "../layout/Loader/FeeSidebar";

const CollegeStudentDetails = () => {
  const dispatch = useDispatch();
  const { id } = useParams();

  const { error, collegeStudent } = useSelector((state) => state.collegeStudentDetails);
  const { error: paymentError, isPaid ,isUpdated } = useSelector(
    (state) => state.collegeStudentProfile
  );
  console.log(collegeStudent);
  const [isOpen, setIsOpen] = useState(0);
  const [password, setPassword] = useState();
  const [amount, setAmount] = useState();
  const [paidBy,setPaidBy] = useState();
  const [receiptNo,setReceiptNo] = useState();
  const [discount,setDiscount] = useState();
  const [hostel,setHostel] = useState();

  const openDialogP = () => {
    setIsOpen(1);
  };
 
  const openDialogU = () => {
    setIsOpen(2);
  }
  const closeDialog = () => {
    setPassword();
    setIsOpen(0);
  };

  const paymentSubmit = (e) => {
    e.preventDefault();
    const data = {
        password,
        receiptNo,
        paidBy,
        amount
    }
    closeDialog();
    try {
      dispatch(payFeesCollege(id,data));
    } catch (err) {}
  };

  const updateSubmit = (e) => {
    e.preventDefault();
    const data = {
        password,
        discount,
        hostel,
        semester:collegeStudent.semester + 1,
    }
    closeDialog();
    try {
      dispatch(updateSemester(id,data));
    } catch (err) {}
  };

  const successFee = () => {
    toast.success("Fee Paid Successfully");
  };
  const successUpdateSem = () => {
    toast.success("Semester updated Successfully");
  };

  const failure = (error) => {
    toast.error(error);
  };

  const payments = collegeStudent.feePayments ?  collegeStudent.feePayments.slice(1) : [];

    console.log(isPaid);

  useEffect(() => {
    if (isPaid === true) {
      successFee();
      dispatch({ type: PAYMENT_CSTUDENT_RESET });
    }
    if(isUpdated === true){
      successUpdateSem();
      dispatch({type: UPDATE_SEM_RESET});
    }
    if (error) {
      failure(error);
      dispatch(clearErrors());
    }
    if (paymentError) {
      failure(paymentError);
      dispatch(clearErrors());
    }

    dispatch(getCollegeStudentDetails(id));
    if(collegeStudent) setHostel(collegeStudent.hostel);
  }, [dispatch,isPaid,error,paymentError,id,isUpdated]);

  return (
    <Fragment>
      {(isOpen==1) && (
        <ReactDialogBox
          closeBox={closeDialog}
          modalWidth="28%"
          headerBackgroundColor="rgb(192, 188, 188)"
          headerTextColor="rgb(39, 39, 39)"
          headerHeight="50"
          closeButtonColor="#D92736"
          bodyBackgroundColor="white"
          bodyTextColor="black"
          bodyHeight="35%"
          headerText="Enter Admin Password"
        >
          <div>
            <form className="loginForm" onSubmit={paymentSubmit}>
              <div className="password">
                <input
                  type="text"
                  placeholder="Paid By"
                  value={paidBy}
                  onChange={(e) => setPaidBy(e.target.value)}
                ></input>
              </div>
              <div className="password">
                <input
                  type="number"
                  placeholder="amount"
                  value={amount}
                  onChange={(e) => setAmount(e.target.value)}
                ></input>
              </div>
              <div className="password">
                <input
                  type="text"
                  placeholder="Receipt Number"
                  value={receiptNo}
                  onChange={(e) => setReceiptNo(e.target.value)}
                ></input>
              </div>
              <div className="password">
                <input
                  type="password"
                  placeholder="password"
                  value={password}
                  onChange={(e) => setPassword(e.target.value)}
                ></input>
              </div>
              <button type="submit" className="loginBtn">
                Pay Fee
              </button>
            </form>
          </div>
        </ReactDialogBox>
      )}
      {(isOpen==2) && (
        <ReactDialogBox
          closeBox={closeDialog}
          modalWidth="28%"
          headerBackgroundColor="rgb(192, 188, 188)"
          headerTextColor="rgb(39, 39, 39)"
          headerHeight="50"
          closeButtonColor="#D92736"
          bodyBackgroundColor="white"
          bodyTextColor="black"
          bodyHeight="35%"
          headerText="Enter Admin Password"
        >
          <div>
            <form className="loginForm" onSubmit={updateSubmit}>
              <div className="password">
                <input
                  type="number"
                  placeholder="discount in rupees"
                  value={discount}
                  onChange={(e) => setDiscount(e.target.value)}
                ></input>
              </div>
              <div className="checkb">
               Hostel:
                <label className="checkl">
                  <input
                    type="checkbox"
                    checked={hostel}
                    onChange={()=>{
                        setHostel(!hostel)
                    }}
                  />
                  Yes
               </label>

               <label className="checkl">
                 <input
                   type="checkbox"
                   checked={!hostel}
                   onChange={() => setHostel(!hostel)}
                 />
                 No
               </label>
              </div>
              <div className="password">
                <input
                  type="password"
                  placeholder="password"
                  value={password}
                  onChange={(e) => setPassword(e.target.value)}
                ></input>
              </div>
              <button type="submit" className="loginBtn">
                Upgrade
              </button>
            </form>
          </div>
        </ReactDialogBox>
      )}
      <FeeSidebar/>
      <div className="detailsContainer">
        <h2 className="bookName">{collegeStudent.name}</h2>
        <div className="details">
          <span className="prefix">Roll Number : </span>
          <span className="value">{collegeStudent.rollNo}</span>
        </div>
        <div className="details">
          <span className="prefix">Phone Number : </span>
          <span className="value">{collegeStudent.phone}</span>
        </div>
        <div className="details">
          <span className="prefix">Son/Daughter Of : </span>
          <span className="value">{collegeStudent.sonOf && collegeStudent.sonOf.name}</span>
        </div>
        <div className="details">
          <span className="prefix">parents phone : </span>
          <span className="value">{collegeStudent.sonOf && collegeStudent.sonOf.phone}</span>
        </div>
        <div className="details">
          <span className="prefix">Degree : </span>
          <span className="value">
            {collegeStudent.degree}
          </span>
        </div>
        <div className="details">
          <span className="prefix">Stream : </span>
          <span className="value">
            {collegeStudent.stream}
          </span>
        </div>
        <div className="details">
          <span className="prefix">Semester : </span>
          <span className="value">
            {collegeStudent.semester}
          </span>
        </div>
        <div className="details">
          <span className="prefix">Address : </span>
          <span className="value">
            {collegeStudent.address}
          </span>
        </div>
        <div className="details">
          <span className="prefix">Hostel : </span>
          <span className="value">{collegeStudent.hostel === true ? "yes" : "no"}</span>
        </div>
        <div className="details">
          <span className="prefix">Fee Paid : </span>
          <span className="value">{collegeStudent.feePaid}</span>
        </div>
        <div className="details">
          <span className="prefix">Fee Due : </span>
          <span className="value">{collegeStudent.feeLeft}</span>
        </div>
        <div className="booksToReturn">
          {payments.map((payment) => {
            return (
            <Link to= {`/collegeReceipt/${payment._id}`}>
              <div className="fees">
                <div className="details">
                  <span className="spanD">Receipt Number : </span>
                  <span>{payment.receiptNo}</span>
                </div>
                <div className="details">
                  <span className="spanD">Amount : </span>
                  <span className="spanPaid">{payment.amount}</span>
                </div>
                <div className="details">
                  <span className="spanD">Paid By : </span>
                  <span>{payment.paidBy}</span>
                </div>
                <div className="details">
                  <span className="spanD">Paid On : </span>
                  <span>
                    {payment.paidOn && payment.paidOn.substr(0, 10)}
                  </span>
                </div>
                <div className="details">
                  <span className="spanD">Accepted By : </span>
                  <span>
                    {payment.acceptedBy}
                  </span>
                </div>
              </div>
            </Link>  
            );
          })}
        </div>
        <div className="buttonContainer">
         <button
           className="feeBtn"
           onClick={() => {
             openDialogP();
           }}
         >
           Make Payment
         </button>
        </div>
        <div className="buttonContainer">
         {collegeStudent.semester < 6 && <button
           className="feeBtn"
           onClick={() => {
             openDialogU();
           }}
         >
           Update Sem to {collegeStudent.semester + 1}
         </button>}
        </div>
      </div>
    </Fragment>
  );
};

export default CollegeStudentDetails;

