import {
    CLEAR_ERRORS,
    CREATE_ITEM_FAIL,
    CREATE_ITEM_REQUEST,
    CREATE_ITEM_RESET,
    CREATE_ITEM_SUCCESS,
    UPDATE_ITEM_FAIL,
    UPDATE_ITEM_REQUEST,
    UPDATE_ITEM_RESET,
    UPDATE_ITEM_SUCCESS,
    DELETE_ITEM_FAIL,
    DELETE_ITEM_REQUEST,
    DELETE_ITEM_RESET,
    DELETE_ITEM_SUCCESS,
    ADD_STOCK_FAIL,
    ADD_STOCK_REQUEST,
    ADD_STOCK_RESET,
    ADD_STOCK_SUCCESS,
    REMOVE_STOCK_FAIL,
    REMOVE_STOCK_REQUEST,
    REMOVE_STOCK_RESET,
    REMOVE_STOCK_SUCCESS,
    ALL_ITEMS_FAIL,
    ALL_ITEMS_REQUEST,
    ALL_ITEMS_SUCCESS,
    ITEM_DETAILS_FAIL,
    ITEM_DETAILS_REQUEST,
    ITEM_DETAILS_SUCCESS
} from "../constants/inventoryConstant";


export const createItemReducer = (state = {item:{}}, action) => {
    switch (action.type) {
      case CREATE_ITEM_REQUEST:
        return {
          loading: true,
          isCreated: false,
        };
      case CREATE_ITEM_SUCCESS:
        return {
          ...state,
          loading: false,
          isCreated: action.payload.success,
          item:action.payload.item
        };
      case CREATE_ITEM_FAIL:
        return {
          ...state,
          loading: false,
          error: action.payload,
        };
      case CREATE_ITEM_RESET:
        return {
          ...state,
          isCreated: false,
        };  
      case CLEAR_ERRORS:
        return {
          ...state,
          error: null,
        };
      default:
        return state;
    }
  };


  export const itemProfileReducer = (state = {}, action) => {
    switch (action.type) {
      case UPDATE_ITEM_REQUEST:
      case DELETE_ITEM_REQUEST:
      case ADD_STOCK_REQUEST:
      case REMOVE_STOCK_REQUEST:
        return {
          ...state,
          loading: true,
        };
      case UPDATE_ITEM_SUCCESS:
        return {
          ...state,
          loading: false,
          isUpdated: action.payload,
        };
  
      case DELETE_ITEM_SUCCESS:
        return {
          ...state,
          loading: false,
          isDeleted: action.payload.success,
          message: action.payload.message,
        };
      case ADD_STOCK_SUCCESS:
        return {
          ...state,
          loading: false,
          isAdded: action.payload,
        };
      case REMOVE_STOCK_SUCCESS:
        return {
          ...state,
          loading: false,
          isRemoved: action.payload,
        };
      case UPDATE_ITEM_FAIL:
      case DELETE_ITEM_FAIL:
      case ADD_STOCK_FAIL:
      case REMOVE_STOCK_FAIL:
        return {
          ...state,
          loading: false,
          error: action.payload,
        };
  
      case UPDATE_ITEM_RESET:
        return {
          ...state,
          isUpdated: false,
        };
  
      case DELETE_ITEM_RESET:
        return {
          ...state,
          isDeleted: false,
        };
      case ADD_STOCK_RESET:
        return {
          ...state,
          isAdded: false,
        };
      case REMOVE_STOCK_RESET:
        return {
          ...state,
          isRemoved: false,
        };
      case CLEAR_ERRORS:
        return {
          ...state,
          error: null,
        };
  
      default:
        return state;
    }
  };

  export const allItemsReducer = (state = { items: [] }, action) => {
    switch (action.type) {
      case ALL_ITEMS_REQUEST:
        return {
          ...state,
          loading: true,
        };
      case ALL_ITEMS_SUCCESS:
        return {
          ...state,
          loading: false,
          items: action.payload.items,
          itemsCount: action.payload.itemsCount
        };
  
      case ALL_ITEMS_FAIL:
        return {
          ...state,
          loading: false,
          error: action.payload,
        };
  
      case CLEAR_ERRORS:
        return {
          ...state,
          error: null,
        };
  
      default:
        return state;
    }
  };
  
  export const itemDetailsReducer = (state = { item: {} }, action) => {
    switch (action.type) {
      case ITEM_DETAILS_REQUEST:
        return {
          ...state,
          loading: true,
        };
      case ITEM_DETAILS_SUCCESS:
        return {
          ...state,
          loading: false,
          item: action.payload,
        };
  
      case ITEM_DETAILS_FAIL:
        return {
          ...state,
          loading: false,
          error: action.payload,
        };
  
      case CLEAR_ERRORS:
        return {
          ...state,
          error: null,
        };
  
      default:
        return state;
    }
  };

