import React, { useState, useEffect, Fragment } from "react";
import { useDispatch, useSelector } from "react-redux";
import {
  clearErrors,
  withdrawBook
} from "../../actions/booksActions";
import { useNavigate} from "react-router-dom";
import { toast } from "react-toastify";

import "../Admin/UpdateProfile.css";
import Loader from "../layout/Loader/Loader";
import { WITHDRAW_BOOK_RESET } from "../../constants/booksConstant";
import LibrarySidebar from "../layout/Loader/LibrarySidebar";

const WithdrawBook = () => {
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const { loading, isWithdrawn, error } = useSelector(
    (state) => state.bookProfile
  );


  const [bookNumber, setBookNumber] = useState();
  const [borrowerName, setBorrowerName] = useState();
  const [password, setPassword] = useState();

  const withdrawSubmit = (e) => {
    e.preventDefault();
    try {
      const data = {
        number:bookNumber,
        borrower:borrowerName.toLowerCase(),
        password
      };
      dispatch(withdrawBook(data));
    } catch (err) {}
  };

  const success = () => {
    toast.success("Book Withdraw Successful");
  };
  const failure = (error) => {
    toast.error(error);
  };
  useEffect(() => {
    
    if (error) {
      failure(error);
      dispatch(clearErrors());
    }

    if (isWithdrawn) {
      //  alert.success("Profile updated successfully");
      success();
      navigate("/library");
      dispatch({ type: WITHDRAW_BOOK_RESET});
    }
  }, [dispatch, error, isWithdrawn]);

  return (
    <Fragment>
      {loading ? (
        <Loader />
      ) : (
        <div className="cardUPR">
          <LibrarySidebar/>
          <p>Withdraw Book</p>
          <form className="loginForm" onSubmit={withdrawSubmit}>
            <div className="loginPhone">
              <input
                type="text"
                placeholder="Book Number"
                required
                value={bookNumber}
                onChange={(e) => setBookNumber(e.target.value)}
              />
            </div>
            <div className="name">
              <input
                type="text"
                placeholder="Borrower Name"
                required
                value={borrowerName}
                onChange={(e) => setBorrowerName(e.target.value)}
              />
            </div>
            <div className="password">
              <input
                type="password"
                placeholder="Admin Password"
                required
                value={password}
                onChange={(e) => setPassword(e.target.value)}
              />
            </div>
            <input type="submit" value="Withdraw" className="loginBtn" />
          </form>
        </div>
      )}
    </Fragment>
  );
};
export default WithdrawBook;
