import {
    CREATE_SSTUDENT_FAIL,
    CREATE_SSTUDENT_REQUEST,
    CLEAR_ERRORS,
    CREATE_SSTUDENT_RESET,
    CREATE_SSTUDENT_SUCCESS,
    ALL_SSTUDENTS_FAIL,
    ALL_SSTUDENTS_REQUEST,
    ALL_SSTUDENTS_SUCCESS,
    SSTUDENT_DETAILS_FAIL,
    SSTUDENT_DETAILS_REQUEST,
    SSTUDENT_DETAILS_SUCCESS,
    DELETE_SSTUDENT_FAIL,
    DELETE_SSTUDENT_REQUEST,
    DELETE_SSTUDENT_RESET,
    DELETE_SSTUDENT_SUCCESS,
    UPDATE_SSTUDENT_FAIL,
    UPDATE_SSTUDENT_REQUEST,
    UPDATE_SSTUDENT_RESET,
    UPDATE_SSTUDENT_SUCCESS,
    UPDATE_GRADE_FAIL,
    UPDATE_GRADE_REQUEST,
    UPDATE_GRADE_RESET,
    UPDATE_GRADE_SUCCESS,
    PAYMENT_SSTUDENT_FAIL,
    PAYMENT_SSTUDENT_REQUEST,
    PAYMENT_SSTUDENT_RESET,
    PAYMENT_SSTUDENT_SUCCESS
} from "../constants/schoolStudentConstant";

export const allSchoolStudentsReducer = (state = { schoolStudents: [] }, action) => {
    switch (action.type) {
      case ALL_SSTUDENTS_REQUEST:
        return {
          ...state,
          loading: true,
        };
      case ALL_SSTUDENTS_SUCCESS:
        return {
          ...state,
          loading: false,
          schoolStudents: action.payload,
        };
  
      case ALL_SSTUDENTS_FAIL:
        return {
          ...state,
          loading: false,
          error: action.payload,
        };
  
      case CLEAR_ERRORS:
        return {
          ...state,
          error: null,
        };
  
      default:
        return state;
    }
  };
  
  export const createScoolStudentReducer = (state = {schoolStudent:{}}, action) => {
    switch (action.type) {
      case CREATE_SSTUDENT_REQUEST:
        return {
          loading: true,
          isCreated: false,
        };
      case CREATE_SSTUDENT_SUCCESS:
        return {
          ...state,
          loading: false,
          isCreated: action.payload.success,
          schoolStudent: action.payload.user
        };
      case CREATE_SSTUDENT_FAIL:
        return {
          ...state,
          loading: false,
          error: action.payload,
        };
      case CREATE_SSTUDENT_RESET:
        return {
          ...state,
          isCreated: false,
        };  
      case CLEAR_ERRORS:
        return {
          ...state,
          error: null,
        };
      default:
        return state;
    }
  };
  
  export const schoolStudentProfileReducer = (state = {schoolStudent:{}}, action) => {
    switch (action.type) {
      case UPDATE_SSTUDENT_REQUEST:
      case DELETE_SSTUDENT_REQUEST:
      case PAYMENT_SSTUDENT_REQUEST:
      case UPDATE_GRADE_REQUEST:  
        return {
          ...state,
          loading: true,
        };
      case UPDATE_SSTUDENT_SUCCESS:
      case UPDATE_GRADE_SUCCESS:  
        return {
          ...state,
          loading: false,
          isUpdated: action.payload,
        };
      case PAYMENT_SSTUDENT_SUCCESS:
        return {
            ...state,
            loading:false,
            isPaid: action.payload.success,
            schoolStudent: action.payload.schoolStudent,
        }
      case DELETE_SSTUDENT_SUCCESS:
        return {
          ...state,
          loading: false,
          isDeleted: action.payload.success,
          message: action.payload.message,
        };
      case UPDATE_SSTUDENT_FAIL:
      case DELETE_SSTUDENT_FAIL:
      case PAYMENT_SSTUDENT_FAIL:
      case UPDATE_GRADE_FAIL:  
        return {
          ...state,
          loading: false,
          error: action.payload,
        };
  
      case UPDATE_SSTUDENT_RESET:
      case UPDATE_GRADE_RESET:
        return {
          ...state,
          isUpdated: false,
        };
  
      case DELETE_SSTUDENT_RESET:
        return {
          ...state,
          isDeleted: false,
        };
      case PAYMENT_SSTUDENT_RESET:
        return {
            ...state,
            isPaid: false,
        } 
      case CLEAR_ERRORS:
        return {
          ...state,
          error: null,
        };
      default:
        return state;
    }
  };
  
  export const schoolStudentDetailsReducer = (state = { schoolStudent: {} }, action) => {
    switch (action.type) {
      case SSTUDENT_DETAILS_REQUEST:
        return {
          ...state,
          loading: true,
        };
      case SSTUDENT_DETAILS_SUCCESS:
        return {
          ...state,
          loading: false,
          schoolStudent: action.payload,
        };
  
      case SSTUDENT_DETAILS_FAIL:
        return {
          ...state,
          loading: false,
          error: action.payload,
        };
  
      case CLEAR_ERRORS:
        return {
          ...state,
          error: null,
        };
  
      default:
        return state;
    }
  };