import {
    CREATE_CSTUDENT_FAIL,
    CREATE_CSTUDENT_REQUEST,
    CLEAR_ERRORS,
    CREATE_CSTUDENT_RESET,
    CREATE_CSTUDENT_SUCCESS,
    ALL_CSTUDENTS_FAIL,
    ALL_CSTUDENTS_REQUEST,
    ALL_CSTUDENTS_SUCCESS,
    CSTUDENT_DETAILS_FAIL,
    CSTUDENT_DETAILS_REQUEST,
    CSTUDENT_DETAILS_SUCCESS,
    DELETE_CSTUDENT_FAIL,
    DELETE_CSTUDENT_REQUEST,
    DELETE_CSTUDENT_RESET,
    DELETE_CSTUDENT_SUCCESS,
    UPDATE_CSTUDENT_FAIL,
    UPDATE_CSTUDENT_REQUEST,
    UPDATE_CSTUDENT_RESET,
    UPDATE_CSTUDENT_SUCCESS,
    UPDATE_SEM_FAIL,
    UPDATE_SEM_REQUEST,
    UPDATE_SEM_RESET,
    UPDATE_SEM_SUCCESS,
    PAYMENT_CSTUDENT_FAIL,
    PAYMENT_CSTUDENT_REQUEST,
    PAYMENT_CSTUDENT_RESET,
    PAYMENT_CSTUDENT_SUCCESS
} from "../constants/collegeStudentConstant";

export const allCollegeStudentsReducer = (state = { collegeStudents: [] }, action) => {
    switch (action.type) {
      case ALL_CSTUDENTS_REQUEST:
        return {
          ...state,
          loading: true,
        };
      case ALL_CSTUDENTS_SUCCESS:
        return {
          ...state,
          loading: false,
          collegeStudents: action.payload,
        };
  
      case ALL_CSTUDENTS_FAIL:
        return {
          ...state,
          loading: false,
          error: action.payload,
        };
  
      case CLEAR_ERRORS:
        return {
          ...state,
          error: null,
        };
  
      default:
        return state;
    }
  };
  
  export const createCollegeStudentReducer = (state = {collegeStudent:{}}, action) => {
    switch (action.type) {
      case CREATE_CSTUDENT_REQUEST:
        return {
          loading: true,
          isCreated: false,
        };
      case CREATE_CSTUDENT_SUCCESS:
        return {
          ...state,
          loading: false,
          isCreated: action.payload.success,
          collegeStudent: action.payload.collegeStudent,
        };
      case CREATE_CSTUDENT_FAIL:
        return {
          ...state,
          loading: false,
          error: action.payload,
        };
      case CREATE_CSTUDENT_RESET:
        return {
          ...state,
          isCreated: false,
        };  
      case CLEAR_ERRORS:
        return {
          ...state,
          error: null,
        };
      default:
        return state;
    }
  };
  
  export const collegeStudentProfileReducer = (state = {collegeStudent:{}}, action) => {
    switch (action.type) {
      case UPDATE_CSTUDENT_REQUEST:
      case DELETE_CSTUDENT_REQUEST:
      case PAYMENT_CSTUDENT_REQUEST:
      case UPDATE_SEM_REQUEST:
        return {
          ...state,
          loading: true,
        };
      case UPDATE_CSTUDENT_SUCCESS:
      case UPDATE_SEM_SUCCESS:  
        return {
          ...state,
          loading: false,
          isUpdated: action.payload,
        };
      case PAYMENT_CSTUDENT_SUCCESS:
        return {
            ...state,
            loading:false,
            isPaid: action.payload.success,
            collegeStudent:action.payload.collegeStudent,
        }
      case DELETE_CSTUDENT_SUCCESS:
        return {
          ...state,
          loading: false,
          isDeleted: action.payload.success,
          message: action.payload.message,
        };
      case UPDATE_CSTUDENT_FAIL:
      case DELETE_CSTUDENT_FAIL:
      case PAYMENT_CSTUDENT_FAIL:
      case UPDATE_SEM_FAIL:    
        return {
          ...state,
          loading: false,
          error: action.payload,
        };
  
      case UPDATE_CSTUDENT_RESET:
      case UPDATE_SEM_RESET:  
        return {
          ...state,
          isUpdated: false,
        };
  
      case DELETE_CSTUDENT_RESET:
        return {
          ...state,
          isDeleted: false,
        };
      case PAYMENT_CSTUDENT_RESET:
        return {
            ...state,
            isPaid: false,
        } 
      case CLEAR_ERRORS:
        return {
          ...state,
          error: null,
        };
      default:
        return state;
    }
  };
  
  export const collegeStudentDetailsReducer = (state = { collegeStudent: {} }, action) => {
    switch (action.type) {
      case CSTUDENT_DETAILS_REQUEST:
        return {
          ...state,
          loading: true,
        };
      case CSTUDENT_DETAILS_SUCCESS:
        return {
          ...state,
          loading: false,
          collegeStudent: action.payload,
        };
  
      case CSTUDENT_DETAILS_FAIL:
        return {
          ...state,
          loading: false,
          error: action.payload,
        };
  
      case CLEAR_ERRORS:
        return {
          ...state,
          error: null,
        };
  
      default:
        return state;
    }
  };