import React, { useState, useEffect, Fragment } from "react";
import { useDispatch, useSelector } from "react-redux";
import { useNavigate, useParams } from "react-router-dom";
import { toast } from "react-toastify";

import {
  clearErrors,
  getUserDetails,
  updateUser
} from "../../actions/usersActions";
import "../Admin/UpdateProfile.css";
import Loader from "../layout/Loader/Loader";
import { UPDATE_BOOK_RESET } from "../../constants/booksConstant";
import { UPDATE_USER_RESET } from "../../constants/usersConstant";
import LibrarySidebar from "../layout/Loader/LibrarySidebar";

const UpdateUser = () => {
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const { id } = useParams();

  const { loading, isUpdated, error } = useSelector(
    (state) => state.userProfile
  );
  const {
    loading: userLoading,
    error: userError,
    user,
  } = useSelector((state) => state.userDetails);

  const [name, setName] = useState();
  const [phone, setPhone] = useState();
  const [password,setPassword] = useState();

  const updateSubmit = (e) => {
    e.preventDefault();
    try {
      const data = {
        name,
        phone,
        password
      };
      dispatch(updateUser(id, data));
    } catch (err) {}
  };

  const success = () => {
    toast.success("User Updated Successfully");
  };
  const failure = (error) => {
    toast.error(error);
  };
  useEffect(() => {
    if (!user || (user && user._id !== id)) dispatch(getUserDetails(id));
    else {
      setName(user.name);
      setPhone(user.phone);
    }
    if (error) {
      failure(error);
      dispatch(clearErrors());
    }
    if(userError){
        failure(userError);
        dispatch(clearErrors());
    }
    if (isUpdated) {
      //  alert.success("Profile updated successfully");
      success();
      navigate(`/user/${user._id}`);
      dispatch({ type: UPDATE_USER_RESET });
    }
  }, [dispatch, error, isUpdated, id, user]);

  return (
    <Fragment>
      {loading || userLoading ? (
        <Loader />
      ) : (
        <div className="cardUPR">
          <LibrarySidebar/>
          <p>Update User</p>
          <form className="loginForm" onSubmit={updateSubmit}>
            <div className="loginPhone">
              <input
                type="text"
                placeholder="name"
                required
                value={name}
                onChange={(e) => setName(e.target.value)}
              />
            </div>
            <div className="phone">
              <input
                type="text"
                placeholder="Phone"
                required
                value={phone}
                onChange={(e) => setPhone(e.target.value)}
              />
            </div>
            <div className="password">
              <input
                type="password"
                placeholder="Admin Password"
                required
                value={password}
                onChange={(e) => setPassword(e.target.value)}
              />
            </div>
            <input type="submit" value="Update" className="loginBtn" />
          </form>
        </div>
      )}
    </Fragment>
  );
};
export default UpdateUser;
