import React, { useState, useEffect, Fragment } from "react";
import { useDispatch, useSelector } from "react-redux";
import { clearErrors, returnBook } from "../../actions/booksActions";
import { useNavigate } from "react-router-dom";
import { toast } from "react-toastify";

import "../Admin/UpdateProfile.css";
import Loader from "../layout/Loader/Loader";
import { RETURN_BOOK_RESET } from "../../constants/booksConstant";
import LibrarySidebar from "../layout/Loader/LibrarySidebar";

const ReturnBook = () => {
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const { loading, isReturned, error } = useSelector(
    (state) => state.bookProfile
  );

  const [bookNumber, setBookNumber] = useState();
  const [returnerName, setReturnerName] = useState();
  const [password, setPassword] = useState();

  const returnSubmit = (e) => {
    e.preventDefault();
    try {
      const data = {
        number: bookNumber,
        returner: returnerName.toLowerCase(),
        password,
      };
      dispatch(returnBook(data));
    } catch (err) {}
  };

  const success = () => {
    toast.success("Book Return Successful");
  };
  const failure = (error) => {
    toast.error(error);
  };
  useEffect(() => {
    if (error) {
      failure(error);
      dispatch(clearErrors());
    }

    if (isReturned) {
      //  alert.success("Profile updated successfully");
      success();
      navigate("/library");
      dispatch({ type: RETURN_BOOK_RESET });
    }
  }, [dispatch, error, isReturned]);

  return (
    <Fragment>
      {loading ? (
        <Loader />
      ) : (
        <div className="cardUPR">
          <LibrarySidebar/>
          <p>Return Book</p>
          <form className="loginForm" onSubmit={returnSubmit}>
            <div className="loginPhone">
              <input
                type="text"
                placeholder="Book Number"
                required
                value={bookNumber}
                onChange={(e) => setBookNumber(e.target.value)}
              />
            </div>
            <div className="name">
              <input
                type="text"
                placeholder="Returner Name"
                required
                value={returnerName}
                onChange={(e) => setReturnerName(e.target.value)}
              />
            </div>
            <div className="password">
              <input
                type="password"
                placeholder="Admin Password"
                required
                value={password}
                onChange={(e) => setPassword(e.target.value)}
              />
            </div>
            <input type="submit" value="Return" className="loginBtn" />
          </form>
        </div>
      )}
    </Fragment>
  );
};
export default ReturnBook;
