import {
    ALL_STAFF_FAIL,
    ALL_STAFF_REQUEST,
    ALL_STAFF_SUCCESS,
    STAFF_DETAILS_FAIL,
    STAFF_DETAILS_REQUEST,
    STAFF_DETAILS_SUCCESS,
    CLEAR_ERRORS,
    CREATE_STAFF_FAIL,
    CREATE_STAFF_REQUEST,
    CREATE_STAFF_RESET,
    CREATE_STAFF_SUCCESS,
    UPDATE_STAFF_FAIL,
    UPDATE_STAFF_REQUEST,
    UPDATE_STAFF_RESET,
    UPDATE_STAFF_SUCCESS,
    DELETE_STAFF_FAIL,
    DELETE_STAFF_REQUEST,
    DELETE_STAFF_RESET,
    DELETE_STAFF_SUCCESS
} from "../constants/staffConstant"

import axios from "axios"
import { CREATE_USER_FAIL } from "../constants/usersConstant";

export const createStaff = (userData) => async(dispatch) => {
    try{
        dispatch({type:CREATE_STAFF_REQUEST});
        const config = { headers: { "Content-Type": "application/json" } };
        const {data} = await axios.post(`/api/v1/staff/create`,userData,config);
        dispatch({
            type:CREATE_STAFF_SUCCESS,
            payload:data
        })
    } 
    catch(error){
        dispatch({
            type:CREATE_USER_FAIL,
            payload:error.response.data.message,
        })
    }
};

export const getAllStaff = (option = "name" , value = "" ) => async(dispatch) => {
    try{
          dispatch({
            type:ALL_STAFF_REQUEST
          });
          const {data} = await axios.get(`/api/v1/staffs?${option}=${value}`);
          dispatch({
            type:ALL_STAFF_SUCCESS,
            payload:data
          })
    }
    catch(error){
        dispatch({
            type:ALL_STAFF_FAIL,
            payload:error
        })
    }
}

// get staff details
export const getStaffDetails = (id) => async(dispatch) =>{
    try{
         dispatch({
            type:STAFF_DETAILS_REQUEST
         })
        const {data} = await axios.get(`/api/v1/staff/${id}`);
        dispatch({
            type:STAFF_DETAILS_SUCCESS,
            payload:data.staff
        })
    }
    catch(error){
        dispatch({
            type:STAFF_DETAILS_FAIL,
            payload:error.response.data.message
        })
    }
}

// Update User
export const updateStaff = (id, userData) => async (dispatch) => {
    try {
      dispatch({ type: UPDATE_STAFF_REQUEST });
  
      const config = { headers: { "Content-Type": "application/json" } };
  
      const { data } = await axios.put(`/api/v1/staff/${id}`, userData, config);
  
      dispatch({ type: UPDATE_STAFF_SUCCESS, payload: data.success });
    } catch (error) {
      dispatch({
        type: UPDATE_STAFF_FAIL,
        payload: error.response.data.message,
      });
    }
  };

  
  // Delete User
  export const deleteStaff = (id, password) => async (dispatch) => {
    try {
      dispatch({ type: DELETE_STAFF_REQUEST });
  
      const { data } = await axios.post(`/api/v1/staff/${id}`, { password });
  
      dispatch({ type: DELETE_STAFF_SUCCESS, payload: data });
    } catch (error) {
      dispatch({
        type: DELETE_STAFF_FAIL,
        payload: error.response.data.message,
      });
    }
  };
  
  // to clear errors
  export const clearErrors = () => async (dispatch) => {
    dispatch({
      type: CLEAR_ERRORS,
    });
  };