import React,{useEffect} from "react";
import { useParams } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import { getBookDetails,clearErrors } from "../../actions/booksActions";
import {toast} from "react-toastify"; 

import "./BookDetails.css"
import LibrarySidebar from "../layout/Loader/LibrarySidebar";

const BookDetails = () => {
    const dispatch = useDispatch();
    const {id} = useParams();

    const {error,book} = useSelector(state => state.bookDetails);

    const failure = (error) => {
      toast.error(error);
    };

    useEffect(()=>{
        if(error){
            failure(error);
            dispatch(clearErrors());     
        }
       dispatch(getBookDetails(id));
    },[])

    return (
      <>
      <LibrarySidebar/>
      <div className="detailsContainer">
        <h2 className="bookName">{book.name}</h2>
        <div className="details">
          <span className="prefix">Book Id Number : </span>
          <span className="value">{book.number}</span>
        </div>
        <div className="details">
          <span className="prefix">Written By : </span>
          <span className="value">{book.author}</span>
        </div>
        <div className="details">
          <span className="prefix">Category : </span>
          <span className="value">{book.category}</span>
        </div>
        <div className="details">
          <span className="prefix">Price : </span>
          <span className="value">{book.cost}</span>
        </div>
        <div className="details">
          <span className="prefix">currently with : </span>
          <span className="value">{book.bookWith}</span>
        </div>
        <div className="details">
          <span className="prefix">last permission for issue by : </span>
          <span className="value">{book.lastGiver}</span>
        </div>
        <div className="details">
          <span className="prefix">last permission for return by : </span>
          <span className="value">{book.lastAcceptor}</span>
        </div>
        <div className="details">
          <span className="prefix">Year Of Edition : </span>
          <span className="value">{book.yearOfEdition}</span>
        </div>
      </div>
      </>
    );
}

export default BookDetails;