import{
    CREATE_FEE_FAIL,
    CREATE_FEE_REQUEST,
    CREATE_FEE_RESET,
    CREATE_FEE_SUCCESS,
    UPDATE_FEE_FAIL,
    UPDATE_FEE_REQUEST,
    UPDATE_FEE_RESET,
    UPDATE_FEE_SUCCESS,
    ALL_FEES_FAIL,
    ALL_FEES_REQUEST,
    ALL_FEES_SUCCESS,
    DELETE_FEE_FAIL,
    DELETE_FEE_REQUEST,
    DELETE_FEE_RESET,
    DELETE_FEE_SUCCESS,
    DETAILS_FEE_FAIL,
    DETAILS_FEE_REQUEST,
    DETAILS_FEE_RESET,
    DETAILS_FEE_SUCCESS,
    CLEAR_ERRORS
} from "../constants/feeConstant";

export const allFeesReducer = (state = { fees: [] }, action) =>{
    switch (action.type) {
        case ALL_FEES_REQUEST:
          return {
            ...state,
            loading: true,
          };
        case ALL_FEES_SUCCESS:
          return {
            ...state,
            loading: false,
            fees: action.payload,
          };
    
        case ALL_FEES_FAIL:
          return {
            ...state,
            loading: false,
            error: action.payload,
          };
    
        case CLEAR_ERRORS:
          return {
            ...state,
            error: null,
          };
    
        default:
          return state;
      }  
};

export const createFeeReducer = (state = {fee:{}}, action) => {
    switch (action.type) {
      case CREATE_FEE_REQUEST:
        return {
          loading: true,
          isCreated: false,
        };
      case CREATE_FEE_SUCCESS:
        return {
          ...state,
          loading: false,
          isCreated: action.payload.success,
          fee: action.payload.fee
        };
      case CREATE_FEE_FAIL:
        return {
          ...state,
          loading: false,
          error: action.payload,
        };
      case CREATE_FEE_RESET:
        return {
          ...state,
          isCreated: false,
        };  
      case CLEAR_ERRORS:
        return {
          ...state,
          error: null,
        };
      default:
        return state;
    }
  };

  export const feeProfileReducer = (state = {}, action) => {
    switch (action.type) {
      case UPDATE_FEE_REQUEST:
      case DELETE_FEE_REQUEST:
        return {
          ...state,
          loading: true,
        };
      case UPDATE_FEE_SUCCESS:
        return {
          ...state,
          loading: false,
          isUpdated: action.payload,
        };
  
      case DELETE_FEE_SUCCESS:
        return {
          ...state,
          loading: false,
          isDeleted: action.payload.success,
          message: action.payload.message,
        };
      case UPDATE_FEE_FAIL:
      case DELETE_FEE_FAIL:
        return {
          ...state,
          loading: false,
          error: action.payload,
        };
  
      case UPDATE_FEE_RESET:
        return {
          ...state,
          isUpdated: false,
        };
  
      case DELETE_FEE_RESET:
        return {
          ...state,
          isDeleted: false,
        };
      case CLEAR_ERRORS:
        return {
          ...state,
          error: null,
        };
  
      default:
        return state;
    }
  };

  export const feeDetailsReducer = (state = { fee: {} }, action) => {
    switch (action.type) {
      case DETAILS_FEE_REQUEST:
        return {
          ...state,
          loading: true,
        };
      case DETAILS_FEE_SUCCESS:
        return {
          ...state,
          loading: false,
          fee: action.payload,
        };
  
      case DETAILS_FEE_FAIL:
        return {
          ...state,
          loading: false,
          error: action.payload,
        };
  
      case CLEAR_ERRORS:
        return {
          ...state,
          error: null,
        };
  
      default:
        return state;
    }
  };