import React, { useState, useEffect, Fragment } from "react";
import { useDispatch, useSelector } from "react-redux";
import { toast } from "react-toastify";

import "../Admin/UpdateProfile.css";
import Loader from "../layout/Loader/Loader";
import FeeSidebar from "../layout/Loader/FeeSidebar";
import {createFee,clearErrors} from "../../actions/feeActions";
import { CREATE_FEE_RESET } from "../../constants/feeConstant";

const CreateFee = () => {
  const dispatch = useDispatch();
 // const navigate = useNavigate();

  const { loading, isCreated, error } = useSelector(
    (state) => state.newFee
  );

  const [stream,setStream] = useState();
  const [grade,setGrade] = useState();
  const [degree,setDegree] = useState();
  const [semester,setSemester] = useState();
  const [amount,setAmount] = useState();
  const [hostel,setHostel] = useState(false);

  const createSubmit = (e) => {
    e.preventDefault();
    try {
      const data = {
        stream,
        grade,
        degree,
        semester,
        hostel,
        amount
      };
      dispatch(createFee(data));
    } catch (err) {}
  };

  const success = () => {
    toast.success("Fee Created Successfully");
  };
  const failure = (error) => {
    toast.error(error);
  };
  useEffect(() => {
    if (error) {
      failure(error);
      dispatch(clearErrors());
    }

    if (isCreated) {
      //  alert.success("Profile updated successfully");
      success();
      dispatch({ type: CREATE_FEE_RESET });
    }
  }, [dispatch, error, isCreated]);

  return (
    <Fragment>
      {loading ? (
        <Loader />
      ) : (
        <div className="cardUPR">
          <FeeSidebar />
          <p>create new Fee</p>
          <form className="loginForm" onSubmit={createSubmit} novalidate="novalidate">
            <div className="password">
              <input
                type="text"
                placeholder="degree"
                required
                value={degree}
                onChange={(e) => setDegree(e.target.value)}
              />
            </div>
            <div className="password">
              <input
                type="number"
                placeholder="semester"
                required
                value={semester}
                onChange={(e) => setSemester(e.target.value)}
              />
            </div>
            <div className="password">
              <input
                type="text"
                placeholder="stream"
                required
                value={stream}
                onChange={(e) => setStream(e.target.value)}
              />
            </div>
            <div className="password">
              <input
                type="number"
                placeholder="grade"
                required
                value={grade}
                onChange={(e) => setGrade(e.target.value)}
              />
            </div>
            <div className="checkb">
               Hostel:
                <label className="checkl">
                  <input
                    type="checkbox"
                    checked={hostel}
                    onChange={()=>{
                        setHostel(!hostel)
                    }}
                  />
                  Yes
               </label>

               <label className="checkl">
                 <input
                   type="checkbox"
                   checked={!hostel}
                   onChange={() => setHostel(!hostel)}
                 />
                 No
               </label>
           </div>
            <div className="password">
              <input
                type="number"
                placeholder="amount in rupees"
                required
                value={amount}
                onChange={(e) => setAmount(e.target.value)}
              />
            </div>
            <input type="submit" value="Create" className="loginBtn" />
          </form>
        </div>
      )}
    </Fragment>
  );
};
export default CreateFee;
