import React, { useEffect } from "react";
import MisSidebar from "../layout/Loader/MisSidebar";
import logo from "../../images/collegeLogo.png";
import { useDispatch, useSelector } from "react-redux";

import "./LibraryHome.css";
import Loader from "../layout/Loader/Loader.js";
import { getAllStaff } from "../../actions/staffActions.js";

function MisHome() {
    const dispatch = useDispatch();
    const {staffs,loading} = useSelector(state => state.allStaff);

    const staffCount = staffs ? staffs.length : 0;

    const monthsSalaryLeft = (dateString) => {
      const date1 = new Date(dateString);
      const date2 = new Date(Date.now());
    
      // Calculate the difference in months
      const monthsDifference = (date2.getFullYear() - date1.getFullYear()) * 12 + (date2.getMonth() - date1.getMonth())
       + ((date2.getDate() < date1.getDate() && date2.getMonth() > date1.getMonth()) ? -1 : 0);
    
      return monthsDifference;
     }

     let sumOfSalary = 0;

     try{
           staffs && staffs.forEach((staff) => {
               sumOfSalary += (staff.lastMonthPaid ? monthsSalaryLeft(staff.lastMonthPaid)*staff.salary : 0);
           });
     } 
     catch(err) {

     }

    useEffect(()=>{
      dispatch(getAllStaff());
    },[])

    return loading ? (<Loader/>) : (
        <>
          <MisSidebar/>
          <div className="libContainer">
            <img src = {logo} />
            <div className="info">
              Number of staff <span className="blueClass">{staffCount}</span>
            </div>
            <div className="info">
              Salary Left to be paid <span className="blueClass">{sumOfSalary}</span>
            </div>
          </div>
        </>
    )
}

export default MisHome;