import{
    CREATE_FEE_FAIL,
    CREATE_FEE_REQUEST,
    CREATE_FEE_RESET,
    CREATE_FEE_SUCCESS,
    UPDATE_FEE_FAIL,
    UPDATE_FEE_REQUEST,
    UPDATE_FEE_RESET,
    UPDATE_FEE_SUCCESS,
    ALL_FEES_FAIL,
    ALL_FEES_REQUEST,
    ALL_FEES_SUCCESS,
    DELETE_FEE_FAIL,
    DELETE_FEE_REQUEST,
    DELETE_FEE_RESET,
    DELETE_FEE_SUCCESS,
    DETAILS_FEE_FAIL,
    DETAILS_FEE_REQUEST,
    DETAILS_FEE_RESET,
    DETAILS_FEE_SUCCESS,
    CLEAR_ERRORS
} from "../constants/feeConstant";
import axios from "axios";

//create user
export const createFee = (feeData) => async (dispatch) => {
    try {
      dispatch({ type: CREATE_FEE_REQUEST });
  
      const config = { headers: { "Content-Type": "application/json" } };
      const { data } = await axios.post(
        `/api/v1/fee/create`,
        feeData,
        config
      );
      dispatch({ type: CREATE_FEE_SUCCESS, payload: data });
    } catch (error) {
      dispatch({
        type: CREATE_FEE_FAIL,
        payload: error.response.data.message,
      });
    }
  };
  
  // get All Users
  export const getAllFees =
    (category = "stream", value = "") =>
    async (dispatch) => {
      try {
        dispatch({ type: ALL_FEES_REQUEST });
        const { data } = await axios.get(`/api/v1/fees?${category}=${value}`);
  
        dispatch({ type: ALL_FEES_SUCCESS, payload: data.fees });
      } catch (error) {
        dispatch({ type: ALL_FEES_FAIL, payload: error });
      }
    };
  
  // Update Fee
  export const updateFee = (id, feeData) => async (dispatch) => {
    try {
      dispatch({ type: UPDATE_FEE_REQUEST });
  
      const config = { headers: { "Content-Type": "application/json" } };
  
      const { data } = await axios.put(`/api/v1/fee/${id}`, feeData, config);
  
      dispatch({ type: UPDATE_FEE_SUCCESS, payload: data.success });
    } catch (error) {
      dispatch({
        type: UPDATE_FEE_FAIL,
        payload: error.response.data.message,
      });
    }
  };
  

  // Delete Fee
  export const deleteFee = (id, password) => async (dispatch) => {
    try {
      dispatch({ type: DELETE_FEE_REQUEST });
  
      const { data } = await axios.post(`/api/v1/fee/${id}`, { password });
  
      dispatch({ type: DELETE_FEE_SUCCESS, payload: data });
    } catch (error) {
      dispatch({
        type: DELETE_FEE_FAIL,
        payload: error.response.data.message,
      });
    }
  };
  

  //get fee details
  export const getFeeDetails = (id) => async (dispatch) => {
    try {
      dispatch({ type: DETAILS_FEE_REQUEST });
      const { data } = await axios.get(`/api/v1/fee/${id}`);
  
      dispatch({ type: DETAILS_FEE_SUCCESS, payload: data.fee });
    } catch (error) {
      dispatch({ type: DETAILS_FEE_FAIL, payload: error.response.data.message });
    }
  };

  // to clear errors
  export const clearErrors = () => async (dispatch) => {
    dispatch({
      type: CLEAR_ERRORS,
    });
  };
  