import CreateCollegeStudent from "./components/CollegeStudent/CreateCollegeStudent";
import CreateSchoolStudent from "./components/SchoolStudent/CreateSchoolStudent";
import CreateFee from "./components/Fee/CreateFee";
import SearchCollegeStudent from "./components/CollegeStudent/SearchCollegeStudent";
