import BotanyLab from "./components/Inventory/BotanyLab";
import ZoologyLab from "./components/Inventory/ZoologyLab";
import PhysicsLab from "./components/Inventory/PhysicsLab";
import ChemistryLab from "./components/Inventory/ChemistryLab";
import ItLab from "./components/Inventory/ItLab";
import Mess from "./components/Inventory/Mess";
import Infrastructure from "./components/Inventory/Infrastructure";
import MisHome from "./components/Home/MisHome";
import CreateStaff from "./components/Staff/CreateStaff";
