import {
    ALL_STAFF_FAIL,
    ALL_STAFF_REQUEST,
    ALL_STAFF_SUCCESS,
    STAFF_DETAILS_FAIL,
    STAFF_DETAILS_REQUEST,
    STAFF_DETAILS_SUCCESS,
    CLEAR_ERRORS,
    CREATE_STAFF_FAIL,
    CREATE_STAFF_REQUEST,
    CREATE_STAFF_RESET,
    CREATE_STAFF_SUCCESS,
    UPDATE_STAFF_FAIL,
    UPDATE_STAFF_REQUEST,
    UPDATE_STAFF_RESET,
    UPDATE_STAFF_SUCCESS,
    DELETE_STAFF_FAIL,
    DELETE_STAFF_REQUEST,
    DELETE_STAFF_RESET,
    DELETE_STAFF_SUCCESS
} from "../constants/staffConstant"


export const allStaffsReducer = (state = {staffs:[]},action) => {
    switch (action.type) {
         case ALL_STAFF_REQUEST:
            return {
                ...state,
                loading:true,
            };
         case ALL_STAFF_SUCCESS:
            return {
                ...state,
                loading:false,
                staffs : action.payload.staffs,
            };
         case ALL_STAFF_FAIL:
            return {
                ...state,
                loading:false,
                error:action.payload,
            };
         case CLEAR_ERRORS:
            return {
                ...state,
                error:null,
            };
         default:
            return state         
    }
};

export const createStaffReducer = (state = {staff:{}},action)  => {
      switch(action.type){
           case CREATE_STAFF_REQUEST:
              return {
                ...state,
                loading:true,
                isCreated:false,
              }
           case CREATE_STAFF_SUCCESS:
              return {
                ...state,
                loading:false,
                staff:action.payload.staff,
                isCreated:action.payload.success
              }
           case CREATE_STAFF_RESET:
              return {
                ...state,
                isCreated:false, 
              }   
           case CREATE_STAFF_FAIL:
              return {
                ...state,
                loading:false,
                error:action.payload
              }
           case CLEAR_ERRORS:
              return {
                ...state,
                error:null,
              }  
            default:
                return state;   
      }
}

export const staffProfileReducer = (state = {}, action) => {
    switch (action.type) {
      case UPDATE_STAFF_REQUEST:
      case DELETE_STAFF_REQUEST:
        return {
          ...state,
          loading: true,
        };
      case UPDATE_STAFF_SUCCESS:
        return {
          ...state,
          loading: false,
          isUpdated: action.payload,
        };
  
      case DELETE_STAFF_SUCCESS:
        return {
          ...state,
          loading: false,
          isDeleted: action.payload.success,
          message: action.payload.message,
        };
      case UPDATE_STAFF_FAIL:
      case DELETE_STAFF_FAIL:
        return {
          ...state,
          loading: false,
          error: action.payload,
        };
  
      case UPDATE_STAFF_RESET:
        return {
          ...state,
          isUpdated: false,
        };
  
      case DELETE_STAFF_RESET:
        return {
          ...state,
          isDeleted: false,
        };
      case CLEAR_ERRORS:
        return {
          ...state,
          error: null,
        };
  
      default:
        return state;
    }
  };
  
  export const staffDetailsReducer = (state = { staff: {} }, action) => {
    switch (action.type) {
      case STAFF_DETAILS_REQUEST:
        return {
          ...state,
          loading: true,
        };
      case STAFF_DETAILS_SUCCESS:
        return {
          ...state,
          loading: false,
          staff: action.payload,
        };
  
      case STAFF_DETAILS_FAIL:
        return {
          ...state,
          loading: false,
          error: action.payload,
        };
  
      case CLEAR_ERRORS:
        return {
          ...state,
          error: null,
        };
  
      default:
        return state;
    }
  };
  