import React from "react";
import "./Sidebar.css";
import { Link } from "react-router-dom";
import logo from "../../../images/collegeLogo.png";

const FeeSidebar = () => {

  return (
    <div className="sidebar">
      <Link to="/fee">
        <button>Home</button>
      </Link>
      <Link to="/collegeStudent/create">
        <button>
          Create College Student
        </button>
      </Link>
      <Link to="/schoolStudent/create">
        <button>
          Create School Student
        </button>
      </Link>
      <Link to="/fee/create">
        <button>
          create fee
        </button>
      </Link>
      <Link to="/collegeStudent/search">
        <button>
          College Students
        </button>
      </Link>
      <Link to="/schoolStudent/search">
        <button>
          School Students
        </button>
      </Link>
      <Link to="/fee/search">
        <button>
          Fees
        </button>
      </Link>
    </div>
  );
};

export default FeeSidebar;