import React from "react";
import "./Sidebar.css";
import { Link } from "react-router-dom";

const LibrarySidebar = () => {

  return (
    <div className="sidebar">
      <Link to="/mis">
        <button>Home</button>
      </Link>
      <Link to="/staff/create">
        <button>
          Create Staff
        </button>
      </Link>
      <Link to="/staff/search">
        <button>
          Staff
        </button>
      </Link>
    </div>
  );
};

export default LibrarySidebar;