import React, { useState, useEffect, Fragment } from "react";
import { useDispatch, useSelector } from "react-redux";
import { createBook, clearErrors } from "../../actions/booksActions";
import { useNavigate } from "react-router-dom";
import { toast } from "react-toastify";
import { BsFillPersonFill } from "react-icons/bs";

import "../Admin/UpdateProfile.css";
import Loader from "../layout/Loader/Loader";
import adminImage from "../../images/adminImage.png";
import { CREATE_BOOK_RESET } from "../../constants/booksConstant";
import LibrarySidebar from "../layout/Loader/LibrarySidebar";

const CreateBook = () => {
  const dispatch = useDispatch();
  const navigate = useNavigate();

  const { loading, isCreated, error, book } = useSelector(
    (state) => state.newBook
  );

  const [author, setAuthor] = useState();
  const [name, setName] = useState();
  const [number, setNumber] = useState();
  const [cost, setCost] = useState();
  const [yearOfEdition, setYearOfEdition] = useState();
  const [category,setCategory] = useState();

  const createSubmit = (e) => {
    e.preventDefault();
    try {
      const data = {
        author,
        name,
        number,
        category,
        cost: cost ? cost : 500,
        yearOfEdition: yearOfEdition ? yearOfEdition : 0,
      };
      dispatch(createBook(data));
    } catch (err) {}
  };

  const success = () => {
    toast.success("Book Created Successfully");
  };
  const failure = (error) => {
    toast.error(error);
  };
  useEffect(() => {
    if (error) {
      failure(error);
      dispatch(clearErrors());
    }

    if (isCreated) {
      //  alert.success("Profile updated successfully");
      success();
      dispatch({ type: CREATE_BOOK_RESET });
    }
  }, [dispatch, error, isCreated]);

  return (
    <Fragment>
      {loading ? (
        <Loader />
      ) : (
        <div className="cardUPR">
          <LibrarySidebar/>
          <p>Create Book</p>
          <form className="loginForm" onSubmit={createSubmit}>
            <div className="loginPhone">
              <input
                type="text"
                placeholder="name"
                required
                value={name}
                onChange={(e) => setName(e.target.value)}
              />
            </div>
            <div className="Author">
              <input
                type="text"
                placeholder="Author"
                required
                value={author}
                onChange={(e) => setAuthor(e.target.value)}
              ></input>
            </div>
            <div className="password">
              <input
                type="text"
                placeholder="book Number"
                required
                value={number}
                onChange={(e) => setNumber(e.target.value)}
              />
            </div>
            <div className="password">
              <input
                type="text"
                placeholder="Category"
                required
                value={category}
                onChange={(e) => setCategory(e.target.value)}
              />
            </div>
            <div className="password">
              <input
                type="number"
                placeholder="cost"
                required
                value={cost}
                onChange={(e) => setCost(e.target.value)}
              />
            </div>
            <div className="password">
              <input
                type="number"
                placeholder="Year Of Edition"
                required
                value={yearOfEdition}
                onChange={(e) => setYearOfEdition(e.target.value)}
              />
            </div>
            <input type="submit" value="Create" className="loginBtn" />
          </form>
        </div>
      )}
    </Fragment>
  );
};
export default CreateBook;
