import {
    CREATE_SSTUDENT_FAIL,
    CREATE_SSTUDENT_REQUEST,
    CLEAR_ERRORS,
    CREATE_SSTUDENT_RESET,
    CREATE_SSTUDENT_SUCCESS,
    ALL_SSTUDENTS_FAIL,
    ALL_SSTUDENTS_REQUEST,
    ALL_SSTUDENTS_SUCCESS,
    SSTUDENT_DETAILS_FAIL,
    SSTUDENT_DETAILS_REQUEST,
    SSTUDENT_DETAILS_SUCCESS,
    DELETE_SSTUDENT_FAIL,
    DELETE_SSTUDENT_REQUEST,
    DELETE_SSTUDENT_RESET,
    DELETE_SSTUDENT_SUCCESS,
    UPDATE_SSTUDENT_FAIL,
    UPDATE_SSTUDENT_REQUEST,
    UPDATE_SSTUDENT_RESET,
    UPDATE_SSTUDENT_SUCCESS,
    PAYMENT_SSTUDENT_FAIL,
    PAYMENT_SSTUDENT_REQUEST,
    PAYMENT_SSTUDENT_RESET,
    PAYMENT_SSTUDENT_SUCCESS,
    UPDATE_GRADE_FAIL,
    UPDATE_GRADE_REQUEST,
    UPDATE_GRADE_RESET,
    UPDATE_GRADE_SUCCESS
} from "../constants/schoolStudentConstant";
import axios from "axios";

//create student
export const createSchoolStudent = (schoolStudentData) => async (dispatch) => {
    try {
      dispatch({ type: CREATE_SSTUDENT_REQUEST });
  
      const config = { headers: { "Content-Type": "application/json" } };
      const { data } = await axios.post(
        `/api/v1/schoolStudent/create`,
        schoolStudentData,
        config
      );
      dispatch({ type: CREATE_SSTUDENT_SUCCESS, payload: data });
    } catch (error) {
      dispatch({
        type: CREATE_SSTUDENT_FAIL,
        payload: error.response.data.message,
      });
    }
  };
  
  // get All Students
  export const getAllSchoolStudents =
    (category = "name", value = "") =>
    async (dispatch) => {
      try {
        dispatch({ type: ALL_SSTUDENTS_REQUEST });
        const { data } = await axios.get(`/api/v1/schoolStudents?${category}=${value}`);
  
        dispatch({ type: ALL_SSTUDENTS_SUCCESS, payload: data.schoolStudents });
      } catch (error) {
        dispatch({ type: ALL_SSTUDENTS_FAIL, payload: error });
      }
    };
  
  // get  Student Details
  export const getSchoolStudentDetails = (id) => async (dispatch) => {
    try {
      dispatch({ type: SSTUDENT_DETAILS_REQUEST });
      const { data } = await axios.get(`/api/v1/schoolStudent/${id}`);
  
      dispatch({ type: SSTUDENT_DETAILS_SUCCESS, payload: data.schoolStudent });
    } catch (error) {
      dispatch({ type: SSTUDENT_DETAILS_FAIL, payload: error.response.data.message });
    }
  };
  
  // Update Student
  export const updateSchoolStudent = (id, schoolStudentData) => async (dispatch) => {
    try {
      dispatch({ type: UPDATE_SSTUDENT_REQUEST });
  
      const config = { headers: { "Content-Type": "application/json" } };
  
      const { data } = await axios.put(`/api/v1/schoolStudent/${id}`, schoolStudentData, config);
  
      dispatch({ type: UPDATE_SSTUDENT_SUCCESS, payload: data.success });
    } catch (error) {
      dispatch({
        type: UPDATE_SSTUDENT_FAIL,
        payload: error.response.data.message,
      });
    }
  };
  

  // Update Student
  export const updateGrade = (id, schoolStudentData) => async (dispatch) => {
    try {
      dispatch({ type: UPDATE_GRADE_REQUEST });
  
      const config = { headers: { "Content-Type": "application/json" } };
  
      const { data } = await axios.put(`/api/v1/schoolStudent/updateGrade/${id}`, schoolStudentData, config);
  
      dispatch({ type: UPDATE_GRADE_SUCCESS, payload: data.success });
    } catch (error) {
      dispatch({
        type: UPDATE_GRADE_FAIL,
        payload: error.response.data.message,
      });
    }
  };
  
  // fee payment
  export const payFeesSchool = (id,schoolStudentData) => async(dispatch) =>{
    try{
      dispatch({type:PAYMENT_SSTUDENT_REQUEST});
      const config = { headers: { "Content-Type": "application/json" } };
      const {data} = await axios.put(`/api/v1/schoolStudent/payfee/${id}`,schoolStudentData,config);
  
      dispatch({type:PAYMENT_SSTUDENT_SUCCESS,payload:data});
    }
    catch(error){
      dispatch({
        type: PAYMENT_SSTUDENT_FAIL,
        payload: error.response.data.message,
      });
    }
  };
  
  
  // Delete student
  export const deleteSchoolStudent = (id, password) => async (dispatch) => {
    try {
      dispatch({ type: DELETE_SSTUDENT_REQUEST });
  
      const { data } = await axios.post(`/api/v1/schoolStudent/${id}`, { password });
  
      dispatch({ type: DELETE_SSTUDENT_SUCCESS, payload: data });
    } catch (error) {
      dispatch({
        type: DELETE_SSTUDENT_FAIL,
        payload: error.response.data.message,
      });
    }
  };
  
  // to clear errors
  export const clearErrors = () => async (dispatch) => {
    dispatch({
      type: CLEAR_ERRORS,
    });
  };
  