import React, { useState, useEffect, Fragment } from "react";
import { useDispatch, useSelector } from "react-redux";
import { useNavigate, useParams } from "react-router-dom";
import { toast } from "react-toastify";

import {
  clearErrors,
  getCollegeStudentDetails,
  updateCollegeStudent
} from "../../actions/collegeStudentActions";
import "../Admin/UpdateProfile.css";
import Loader from "../layout/Loader/Loader";
import { UPDATE_CSTUDENT_RESET } from "../../constants/collegeStudentConstant";
import FeeSidebar from "../layout/Loader/FeeSidebar";

const UpdateCollegeStudent = () => {
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const { id } = useParams();

  const { loading, isUpdated, error } = useSelector(
    (state) => state.collegeStudentProfile
  );
  const {
    loading: studentLoading,
    error: studentError,
    collegeStudent,
  } = useSelector((state) => state.collegeStudentDetails);

  const [sonOf, setSonOf] = useState();
  const [name, setName] = useState();
  const [rollNo, setRollNo] = useState();
  const [phone, setPhone] = useState();
  const [parentsPhone, setParentsPhone] = useState();
  const [stream,setStream] = useState();
  const [degree,setDegree] = useState();
  const [address,setAddress] = useState();
  const [password,setPassword] = useState();
  const [hostel,setHostel] = useState();

  const updateSubmit = (e) => {
    e.preventDefault();
    try {
        const temp = {
            sonOf : {
                name: sonOf,
                phone:parentsPhone
            }
          }
          const data = {
            ...temp,
            name,
            rollNo,
            phone,
            stream,
            degree,
            address,
            password,
            hostel,
            semester:collegeStudent.semester
          };
      dispatch(updateCollegeStudent(id, data));
    } catch (err) {}
  };

  const success = () => {
    toast.success("Student Updated Successfully");
  };
  const failure = (error) => {
    toast.error(error);
  };
  useEffect(() => {
    if (!collegeStudent || (collegeStudent && collegeStudent._id !== id)) dispatch(getCollegeStudentDetails(id));
    else {
      setName(collegeStudent.name);
      setPhone(collegeStudent.phone);
      setSonOf(collegeStudent.sonOf.name);
      setRollNo(collegeStudent.rollNo);
      setParentsPhone(collegeStudent.sonOf.phone);
      setStream(collegeStudent.stream);
      setDegree(collegeStudent.degree) 
      setAddress(collegeStudent.address) 
      setHostel(collegeStudent.hostel)
    }
    if (error) {
      failure(error);
      dispatch(clearErrors());
    }
    if(studentError){
        failure(studentError);
        dispatch(clearErrors());
    }
    if (isUpdated) {
      //  alert.success("Profile updated successfully");
      success();
      navigate(`/collegeStudent/${collegeStudent._id}`);
      dispatch({ type: UPDATE_CSTUDENT_RESET });
    }
  }, [dispatch, error, isUpdated, id, collegeStudent]);

  return (
    <Fragment>
      {loading || studentLoading ? (
        <Loader />
      ) : (
        <div className="cardUPR">
          <FeeSidebar/>
          <p>Update Student</p>
          <form className="loginForm" onSubmit={updateSubmit}>
            <div className="loginPhone">
              <input
                type="text"
                placeholder="name"
                required
                value={name}
                onChange={(e) => setName(e.target.value)}
              />
            </div>
            <div className="Author">
              <input
                type="text"
                placeholder="son/daughter Of"
                required
                value={sonOf}
                onChange={(e) => setSonOf(e.target.value)}
              ></input>
            </div>
            <div className="password">
              <input
                type="text"
                placeholder="student Phone Number"
                required
                value={phone}
                onChange={(e) => setPhone(e.target.value)}
              />
            </div>
            <div className="password">
              <input
                type="text"
                placeholder="parent's phone"
                required
                value={parentsPhone}
                onChange={(e) => setParentsPhone(e.target.value)}
              />
            </div>
            <div className="password">
              <input
                type="text"
                placeholder="address"
                required
                value={address}
                onChange={(e) => setAddress(e.target.value)}
              />
            </div>
            <div className="password">
              <input
                type="text"
                placeholder="degree"
                required
                value={degree}
                onChange={(e) => setDegree(e.target.value)}
              />
            </div>
            <div className="password">
              <input
                type="text"
                placeholder="stream"
                required
                value={stream}
                onChange={(e) => setStream(e.target.value)}
              />
            </div>
            <div className="password">
              <input
                type="text"
                placeholder="roll number"
                required
                value={rollNo}
                onChange={(e) => setRollNo(e.target.value)}
              />
            </div>
            <div className="checkb">
               Hostel:
                <label className="checkl">
                  <input
                    type="checkbox"
                    checked={hostel}
                    onChange={()=>{
                        setHostel(!hostel)
                    }}
                  />
                  Yes
               </label>

               <label className="checkl">
                 <input
                   type="checkbox"
                   checked={!hostel}
                   onChange={() => setHostel(!hostel)}
                 />
                 No
               </label>
           </div>
            <div className="password">
              <input
                type="password"
                placeholder="admin password"
                required
                value={password}
                onChange={(e) => setPassword(e.target.value)}
              />
            </div>
            <input type="submit" value="Update" className="loginBtn" />
          </form>
        </div>
      )}
    </Fragment>
  );
};
export default UpdateCollegeStudent;
