import React, { useState, useEffect, Fragment } from "react";
import { useDispatch, useSelector } from "react-redux";
import { useNavigate } from "react-router-dom";
import { toast } from "react-toastify";

import { clearErrors, createUser } from "../../actions/usersActions";
import "../Admin/UpdateProfile.css";
import Loader from "../layout/Loader/Loader";
import { CREATE_BOOK_RESET } from "../../constants/booksConstant";
import { CREATE_USER_RESET } from "../../constants/usersConstant";
import LibrarySidebar from "../layout/Loader/LibrarySidebar";

const CreateUser = () => {
  const dispatch = useDispatch();
  const navigate = useNavigate();

  const { loading, isCreated, error, user } = useSelector(
    (state) => state.newUser
  );

  const [phone, setPhone] = useState();
  const [name, setName] = useState();

  const createSubmit = (e) => {
    e.preventDefault();
    try {
      const data = {
        name,
        phone
      };
      dispatch(createUser(data));
    } catch (err) {}
  };

  const success = () => {
    toast.success("User Created Successfully");
  };
  const failure = (error) => {
    toast.error(error);
  };
  useEffect(() => {
    if (error) {
      failure(error);
      dispatch(clearErrors());
    }

    if (isCreated) {
      //  alert.success("Profile updated successfully");
      success();
      navigate(`/user/${user._id}`);
      dispatch({ type: CREATE_USER_RESET });
    }
  }, [dispatch, error, isCreated]);

  return (
    <Fragment>
      {loading ? (
        <Loader />
      ) : (
        <div className="cardUPR">
          <LibrarySidebar/>
          <p>Create User</p>
          <form className="loginForm" onSubmit={createSubmit}>
            <div className="loginPhone">
              <input
                type="text"
                placeholder="phone"
                required
                value={phone}
                onChange={(e) => setPhone(e.target.value)}
              />
            </div>
            <div className="Name">
              <input
                type="text"
                placeholder="Name"
                required
                value={name}
                onChange={(e) => setName(e.target.value)}
              />
            </div>
            <input type="submit" value="Create" className="loginBtn" />
          </form>
        </div>
      )}
    </Fragment>
  );
};
export default CreateUser;
