import React, { Fragment, useEffect } from "react";
import "./LibraryHome.css";
import { useSelector, useDispatch } from "react-redux";
import Loader from "../layout/Loader/Loader.js";

import collegeLogo from "../../images/collegeLogo.png";
import { getAllBooks } from "../../actions/booksActions.js";
import { getAllUsers } from "../../actions/usersActions.js";
import LibraySidebar from "../layout/Loader/LibrarySidebar.js";

function LibraryHome() {
  const dispatch = useDispatch();

  const { books, loading } = useSelector((state) => state.allBooks);
  const { users, loading: userLoading } = useSelector(
    (state) => state.allUsers
  );

  useEffect(() => {
    dispatch(getAllBooks());
    dispatch(getAllUsers());
  }, []);

  const numUsers = users.length;
  const numBooks = books.length;
  let notWithLibrary = 0;
  try {
    books.forEach((book) => {
      if (book.bookWith !== "library") notWithLibrary++;
    });
  } catch (err) {
    console.log(err);
  }
  return loading || userLoading ? (
    <Loader />
  ) : (
  <>
    <LibraySidebar />
    <div className="container">
      <div className="libContainer">
       <img src={collegeLogo} />
       <div className="info">
         Total number of books<span className="blueClass">{numBooks}</span>{" "}
       </div>
       <div className="info">
         Total number of users<span>{numUsers}</span>
       </div>
       <div className="info">
         Books issued from library<span className="redClass">{notWithLibrary}</span>
       </div>
      </div>
    </div>
  </>
  );
}

export default LibraryHome;
