import React from "react";
import "./Sidebar.css";
import { Link } from "react-router-dom";
import logo from "../../../images/collegeLogo.png";

const InventorySidebar = () => {

  return (
    <div className="sidebar">
      <Link to="/inventory">
        <button>Home</button>
      </Link>
      <Link to="/item/create">
        <button>
          Create Item
        </button>
      </Link>
      <Link to="/lab/physics">
        <button>
          Physics
        </button>
      </Link>
      <Link to="/lab/chemistry">
        <button>
          Chemistry
        </button>
      </Link>
      <Link to="/lab/botany">
        <button>
          Botany
        </button>
      </Link>
      <Link to="/lab/zoology">
        <button>
          Zoology
        </button>
      </Link>
      <Link to="/lab/it">
        <button>
          I.T
        </button>
      </Link>
      <Link to="/lab/mess">
        <button>
          Mess
        </button>
      </Link>
      <Link to="/lab/infrastructure">
        <button>
          Infrastructure
        </button>
      </Link>
    </div>
  );
};

export default InventorySidebar;