import React, { useState, useEffect, Fragment } from "react";
import { useDispatch, useSelector } from "react-redux";
import { useNavigate } from "react-router-dom";
import { toast } from "react-toastify";

import "../Admin/UpdateProfile.css";
import Loader from "../layout/Loader/Loader";
import FeeSidebar from "../layout/Loader/FeeSidebar";
import { clearErrors, createCollegeStudent } from "../../actions/collegeStudentActions";
import { CREATE_CSTUDENT_RESET } from "../../constants/collegeStudentConstant";

const CreateCollegeStudent = () => {
  const dispatch = useDispatch();
 // const navigate = useNavigate();

  const { loading, isCreated, error } = useSelector(
    (state) => state.newCollegeStudent
  );

  const [sonOf, setSonOf] = useState();
  const [name, setName] = useState();
  const [rollNo, setRollNo] = useState();
  const [phone, setPhone] = useState();
  const [parentsPhone, setParentsPhone] = useState();
  const [stream,setStream] = useState();
  const [degree,setDegree] = useState();
  const [semester,setSemester] = useState();
  const [discounted,setDiscounted] = useState();
  const [address,setAddress] = useState();
  const [hostel,setHostel] = useState(false);

  const createSubmit = (e) => {
    e.preventDefault();
    try {
      const temp = {
        sonOf : {
            name: sonOf,
            phone:parentsPhone
        }
      }
      const data = {
        ...temp,
        name,
        rollNo,
        phone,
        stream,
        degree,
        semester,
        address,
        hostel,
        discounted
      };
      dispatch(createCollegeStudent(data));
    } catch (err) {}
  };

  const success = () => {
    toast.success("Student Created Successfully");
  };
  const failure = (error) => {
    toast.error(error);
  };
  useEffect(() => {
    if (error) {
      failure(error);
      dispatch(clearErrors());
    }

    if (isCreated) {
      //  alert.success("Profile updated successfully");
      success();
      dispatch({ type: CREATE_CSTUDENT_RESET });
    }
  }, [dispatch, error, isCreated]);

  return (
    <Fragment>
      {loading ? (
        <Loader />
      ) : (
        <div className="cardUPR">
          <FeeSidebar />
          <p>create new degree student</p>
          <form className="loginForm" onSubmit={createSubmit}>
            <div className="loginPhone">
              <input
                type="text"
                placeholder="name"
                required
                value={name}
                onChange={(e) => setName(e.target.value)}
              />
            </div>
            <div className="Author">
              <input
                type="text"
                placeholder="son/daughter Of"
                required
                value={sonOf}
                onChange={(e) => setSonOf(e.target.value)}
              ></input>
            </div>
            <div className="password">
              <input
                type="text"
                placeholder="student Phone Number"
                required
                value={phone}
                onChange={(e) => setPhone(e.target.value)}
              />
            </div>
            <div className="password">
              <input
                type="text"
                placeholder="parent's phone"
                required
                value={parentsPhone}
                onChange={(e) => setParentsPhone(e.target.value)}
              />
            </div>
            <div className="password">
              <input
                type="text"
                placeholder="address"
                required
                value={address}
                onChange={(e) => setAddress(e.target.value)}
              />
            </div>
            <div className="password">
              <input
                type="text"
                placeholder="degree"
                required
                value={degree}
                onChange={(e) => setDegree(e.target.value)}
              />
            </div>
            <div className="password">
              <input
                type="text"
                placeholder="stream"
                required
                value={stream}
                onChange={(e) => setStream(e.target.value)}
              />
            </div>
            <div className="password">
              <input
                type="number"
                placeholder="semester"
                required
                value={semester}
                onChange={(e) => setSemester(e.target.value)}
              />
            </div>
            <div className="password">
              <input
                type="text"
                placeholder="roll number"
                required
                value={rollNo}
                onChange={(e) => setRollNo(e.target.value)}
              />
            </div>
            <div className="checkb">
               Hostel:
                <label className="checkl">
                  <input
                    type="checkbox"
                    checked={hostel}
                    onChange={()=>{
                        setHostel(!hostel)
                    }}
                  />
                  Yes
               </label>

               <label className="checkl">
                 <input
                   type="checkbox"
                   checked={!hostel}
                   onChange={() => setHostel(!hostel)}
                 />
                 No
               </label>
           </div>
            <div className="password">
              <input
                type="number"
                placeholder="discounted amount in rupees"
                required
                value={discounted}
                onChange={(e) => setDiscounted(e.target.value)}
              />
            </div>
            <input type="submit" value="Create" className="loginBtn" />
          </form>
        </div>
      )}
    </Fragment>
  );
};
export default CreateCollegeStudent;
