import React, { useEffect } from "react";
import FeeSidebar from "../layout/Loader/FeeSidebar";
import logo from "../../images/collegeLogo.png";
import { useDispatch, useSelector } from "react-redux";

import "./LibraryHome.css";
import Loader from "../layout/Loader/Loader.js";
import { getAllCollegeStudents } from "../../actions/collegeStudentActions";
import { getAllSchoolStudents } from "../../actions/schoolStudentActions";

function FeeHome() {
    
    const dispatch = useDispatch();
    const {collegeStudents,loading} = useSelector(state => state.allCollegeStudents);
    const {schoolStudents, loading: schoolLoading} = useSelector(state => state.allSchoolStudents);

    useEffect(()=>{
      dispatch(getAllCollegeStudents());
      dispatch(getAllSchoolStudents());
    },[])

    const collegeStrength = collegeStudents.length; 
    const schoolStrength = schoolStudents.length;

    let feeDueCollege = 0;
    let feeDueSchool = 0;

    try {
      collegeStudents.forEach((collegeStudent) => {
        feeDueCollege += Number(collegeStudent.feeLeft);
      });

      schoolStudents.forEach((schoolStudent) => {
        feeDueSchool += Number(schoolStudent.feeLeft)
      })
    } catch (err) {
      console.log(err);
    }

    return loading || schoolLoading ? (<Loader/>) : (
        <>
          <FeeSidebar/>
          <div className="libContainer">
            <img src = {logo} />
            <div className="info">
              Number of students in degree <span className="blueClass">{collegeStrength}</span>
            </div>
            <div className="info">
              Number of students in school <span className="blueClass">{schoolStrength}</span>
            </div>
            <div className="info">
              Fees due in degree <span className="redClass">{feeDueCollege}</span>
            </div>
            <div className="info">
              Fees due in school <span className="redClass">{feeDueSchool}</span>
            </div>
          </div>
        </>
    )
}

export default FeeHome;