import {
    CREATE_CSTUDENT_FAIL,
    CREATE_CSTUDENT_REQUEST,
    CLEAR_ERRORS,
    CREATE_CSTUDENT_RESET,
    CREATE_CSTUDENT_SUCCESS,
    ALL_CSTUDENTS_FAIL,
    ALL_CSTUDENTS_REQUEST,
    ALL_CSTUDENTS_SUCCESS,
    CSTUDENT_DETAILS_FAIL,
    CSTUDENT_DETAILS_REQUEST,
    CSTUDENT_DETAILS_SUCCESS,
    DELETE_CSTUDENT_FAIL,
    DELETE_CSTUDENT_REQUEST,
    DELETE_CSTUDENT_RESET,
    DELETE_CSTUDENT_SUCCESS,
    UPDATE_CSTUDENT_FAIL,
    UPDATE_CSTUDENT_REQUEST,
    UPDATE_CSTUDENT_RESET,
    UPDATE_CSTUDENT_SUCCESS,
    PAYMENT_CSTUDENT_FAIL,
    PAYMENT_CSTUDENT_REQUEST,
    PAYMENT_CSTUDENT_RESET,
    PAYMENT_CSTUDENT_SUCCESS,
    UPDATE_SEM_FAIL,
    UPDATE_SEM_REQUEST,
    UPDATE_SEM_RESET,
    UPDATE_SEM_SUCCESS
} from "../constants/collegeStudentConstant";

import axios from "axios";


//create student
export const createCollegeStudent = (collegeStudentData) => async (dispatch) => {
    try {
      dispatch({ type: CREATE_CSTUDENT_REQUEST });
  
      const config = { headers: { "Content-Type": "application/json" } };
      const { data } = await axios.post(
        `/api/v1/collegeStudent/create`,
        collegeStudentData,
        config
      );
      dispatch({ type: CREATE_CSTUDENT_SUCCESS, payload: data });
    } catch (error) {
      dispatch({
        type: CREATE_CSTUDENT_FAIL,
        payload: error.response.data.message,
      });
    }
  };
  
  // get All Students
  export const getAllCollegeStudents =
    (category = "name", value = "") =>
    async (dispatch) => {
      try {
        dispatch({ type: ALL_CSTUDENTS_REQUEST });
        const { data } = await axios.get(`/api/v1/collegeStudents?${category}=${value}`);
  
        dispatch({ type: ALL_CSTUDENTS_SUCCESS, payload: data.collegeStudents });
      } catch (error) {
        dispatch({ type: ALL_CSTUDENTS_FAIL, payload: error });
      }
    };
  
  // get  Student Details
  export const getCollegeStudentDetails = (id) => async (dispatch) => {
    try {
      dispatch({ type: CSTUDENT_DETAILS_REQUEST });
      const { data } = await axios.get(`/api/v1/collegeStudent/${id}`);
  
      dispatch({ type: CSTUDENT_DETAILS_SUCCESS, payload: data.collegeStudent });
    } catch (error) {
      dispatch({ type: CSTUDENT_DETAILS_FAIL, payload: error.response.data.message });
    }
  };
  
  // Update Student
  export const updateCollegeStudent = (id, collegeStudentData) => async (dispatch) => {
    try {
      dispatch({ type: UPDATE_CSTUDENT_REQUEST });
  
      const config = { headers: { "Content-Type": "application/json" } };
  
      const { data } = await axios.put(`/api/v1/collegeStudent/${id}`, collegeStudentData, config);
  
      dispatch({ type: UPDATE_CSTUDENT_SUCCESS, payload: data.success });
    } catch (error) {
      dispatch({
        type: UPDATE_CSTUDENT_FAIL,
        payload: error.response.data.message,
      });
    }
  };


  export const updateSemester = (id, collegeStudentData) => async (dispatch) => {
    try {
      dispatch({ type: UPDATE_SEM_REQUEST });
  
      const config = { headers: { "Content-Type": "application/json" } };
  
      const { data } = await axios.put(`/api/v1/collegeStudent/updateSem/${id}`, collegeStudentData, config);
  
      dispatch({ type: UPDATE_SEM_SUCCESS, payload: data.success });
    } catch (error) {
      dispatch({
        type: UPDATE_SEM_FAIL,
        payload: error.response.data.message,
      });
    }
  };
  
  
  // fine user
  export const payFeesCollege = (id,collegeStudentData) => async(dispatch) =>{
    try{
      dispatch({type:PAYMENT_CSTUDENT_REQUEST});
      const config = { headers: { "Content-Type": "application/json" } };
      const {data} = await axios.put(`/api/v1/collegeStudent/payfee/${id}`,collegeStudentData,config);
  
      dispatch({type:PAYMENT_CSTUDENT_SUCCESS,payload:data});
    }
    catch(error){
      dispatch({
        type: PAYMENT_CSTUDENT_FAIL,
        payload: error.response.data.message,
      });
    }
  };
  
  
  // Delete User
  export const deleteCollegeStudent = (id, password) => async (dispatch) => {
    try {
      dispatch({ type: DELETE_CSTUDENT_REQUEST });
  
      const { data } = await axios.post(`/api/v1/collegeStudent/${id}`, { password });
  
      dispatch({ type: DELETE_CSTUDENT_SUCCESS, payload: data });
    } catch (error) {
      dispatch({
        type: DELETE_CSTUDENT_FAIL,
        payload: error.response.data.message,
      });
    }
  };
  
  // to clear errors
  export const clearErrors = () => async (dispatch) => {
    dispatch({
      type: CLEAR_ERRORS,
    });
  };
  