import {
    CLEAR_ERRORS,
    CREATE_ITEM_FAIL,
    CREATE_ITEM_REQUEST,
    CREATE_ITEM_RESET,
    CREATE_ITEM_SUCCESS,
    UPDATE_ITEM_FAIL,
    UPDATE_ITEM_REQUEST,
    UPDATE_ITEM_RESET,
    UPDATE_ITEM_SUCCESS,
    DELETE_ITEM_FAIL,
    DELETE_ITEM_REQUEST,
    DELETE_ITEM_RESET,
    DELETE_ITEM_SUCCESS,
    ADD_STOCK_FAIL,
    ADD_STOCK_REQUEST,
    ADD_STOCK_RESET,
    ADD_STOCK_SUCCESS,
    REMOVE_STOCK_FAIL,
    REMOVE_STOCK_REQUEST,
    REMOVE_STOCK_RESET,
    REMOVE_STOCK_SUCCESS,
    ALL_ITEMS_FAIL,
    ALL_ITEMS_REQUEST,
    ALL_ITEMS_SUCCESS,
    ITEM_DETAILS_FAIL,
    ITEM_DETAILS_REQUEST,
    ITEM_DETAILS_SUCCESS
} from "../constants/inventoryConstant";
import axios from "axios";

export const createItem = (userData) => async (dispatch) => {
    try {
      dispatch({ type: CREATE_ITEM_REQUEST });
  
    const config = { headers: { "Content-Type": "application/json" } };
      const { data } = await axios.post(`/api/v1/createStock`, userData, config);
      dispatch({ type: CREATE_ITEM_SUCCESS, payload: data });
    } catch (error) {
      dispatch({
        type: CREATE_ITEM_FAIL,
        payload: error.response.data.message,
      });
    }
  };
  
  
    // get All Items
  export const getAllItems = (lab,value="") => async (dispatch) => {
    try {
      dispatch({ type: ALL_ITEMS_REQUEST });
      const { data } = await axios.get(`/api/v1/stocks?lab=${lab}&name=${value}`);
  
      dispatch({ type: ALL_ITEMS_SUCCESS, payload: data });
    } catch (error) {
      dispatch({ type: ALL_ITEMS_FAIL, payload: error.response.data.message });
    }
  };
  
  // get  Item Details
  export const getItemDetails = (id) => async (dispatch) => {
    try {
      dispatch({ type: ITEM_DETAILS_REQUEST });
      const { data } = await axios.get(`/api/v1/stock/${id}`);
  
      dispatch({ type: ITEM_DETAILS_SUCCESS, payload: data.item });
    } catch (error) {
      dispatch({ type: ITEM_DETAILS_FAIL, payload: error.response.data.message });
    }
  };
  
  // Update Item
  export const updateItem = (id, userData) => async (dispatch) => {
    try {
      dispatch({ type: UPDATE_ITEM_REQUEST });
  
      const config = { headers: { "Content-Type": "application/json" } };
  
      const { data } = await axios.put(
        `/api/v1/stock/${id}`,
        userData,
        config
      );
  
      dispatch({ type: UPDATE_ITEM_SUCCESS, payload: data.success });
    } catch (error) {
      dispatch({
        type: UPDATE_ITEM_FAIL,
        payload: error.response.data.message,
      });
    }
  };
  
  
  
  // Add to Stock
  export const addStock = (id,userData) => async (dispatch) => {
    try {
      dispatch({ type: ADD_STOCK_REQUEST});
  
      const config = { headers: { "Content-Type": "application/json" } };
  
      const { data } = await axios.put(`/api/v1/stock/add/${id}`, userData, config);
  
      dispatch({ type: ADD_STOCK_SUCCESS, payload: data.success });
    } catch (error) {
      dispatch({
        type: ADD_STOCK_FAIL,
        payload: error.response.data.message,
      });
    }
  };
  
  
  // Remove from stock
  export const removeStock = (id,userData) => async (dispatch) => {
    try {
      dispatch({ type: REMOVE_STOCK_REQUEST});
  
      const config = { headers: { "Content-Type": "application/json" } };
  
      const { data } = await axios.put(`/api/v1/stock/remove/${id}`, userData, config);
  
      dispatch({ type: REMOVE_STOCK_SUCCESS, payload: data.success });
    } catch (error) {
      dispatch({
        type: REMOVE_STOCK_FAIL,
        payload: error.response.data.message,
      });
    }
  };
  
  
  // Delete Item
  export const deleteItem = (id,password) => async (dispatch) => {
    try {
      dispatch({ type: DELETE_ITEM_REQUEST });
  
      const { data } = await axios.post(`/api/v1/stock/${id}`,{password});
  
      dispatch({ type: DELETE_ITEM_SUCCESS, payload: data });
    } catch (error) {
      dispatch({
        type: DELETE_ITEM_FAIL,
        payload: error.response.data.message,
      });
    }
  };
  
  // to clear errors
  export const clearErrors = () => async(dispatch) => {
      dispatch({
         type: CLEAR_ERRORS
      });
  };
  