import React, { Fragment, useEffect, useState } from "react";
import { useSelector, useDispatch } from "react-redux";
import DataTable from "react-data-table-component";
import { ReactDialogBox } from "react-js-dialog-box";
import "react-js-dialog-box/dist/index.css";

import "../Books/SearchBook.css";
import {
  clearErrors,
  getAllItems,
  deleteItem,
  addStock,
  removeStock
} from "../../actions/inventoryActions";
import { toast } from "react-toastify";
import { ADD_STOCK_RESET, DELETE_ITEM_RESET, REMOVE_STOCK_RESET } from "../../constants/inventoryConstant";
import InventorySidebar from "../layout/Loader/InventorySidebar";

const Mess = () => {
  const dispatch = useDispatch();

  const { error, items } = useSelector((state) => state.allItems);

  const { error: deleteError, isDeleted,isAdded,isRemoved } = useSelector(
    (state) => state.itemProfile
  );

  const [isOpen, setIsOpen] = useState(0);
  const [password, setPassword] = useState();
  const [id, setId] = useState();
  const [value, setValue] = useState(); 
  const [costd,setCostd] = useState();
  const [stockd,setStockd] = useState();
  const [askedBy,setAskedBy] = useState();
  
  let nameSort = 0;
  let costSort = 0;
  let stockSort = 0;
  let damagedSort = 0;
  
  const openDialogDel = () => {
    setIsOpen(1);
  };

  const openDialogAdd = () =>{
    setIsOpen(2);
  }

  const openDialogRem = () =>{
    setIsOpen(3);
  }

  const closeDialog = () => {
    setPassword();
    setIsOpen(0);
  };

  const makeId = (id) => {
    setId(id);
  };

  const deleteSubmit = () => {
    const temp = password; // kyuki close dialog password ko null krdega toh pehle hi store krna pdega
    closeDialog();
    dispatch(deleteItem(id, temp));
  };

  const addSubmit = () =>{
    const temp = password;
    closeDialog();
    const data = {
        cost:costd,
        stock:stockd,
        askedBy,
        password:temp,
    }
    dispatch(addStock(id,data));
  }

  const removeSubmit = () =>{
    const temp = password;
    closeDialog();
    const data = {
        stock:stockd,
        askedBy,
        password:temp,
    }
    dispatch(removeStock(id,data));
  }

  const successDelete = () => {
    toast.success("Item Deleted Successfully");
  };
  const successAdd = () => {
    toast.success("Stock added Successfully");
  };
  const successRemove = () => {
    toast.success("Stock Removed Successfully");
  };
  const failure = (error) => {
    toast.error(error);
  };


  const sortFunction = (type) => {

    if (type === "name") {
      nameSort++;
      if (nameSort % 2 === 0) items.sort((a, b) => b.name.localeCompare(a.name));
      else items.sort((a, b) => a.name.localeCompare(b.name));
    } 
    
    else if (type === "cost") {
      costSort++;
      if (costSort % 2 === 0) items.sort((a, b) => b.cost - a.cost);
      else items.sort((a, b) => a.cost - b.cost);
    }
    
    else if(type === "stock"){
        stockSort++;
        if(stockSort%2 === 0) items.sort((a,b) => b.stock - a.stock);
        else items.sort((a,b) => a.stock - b.stock);
    }
    else {
        damagedSort++;
        if(damagedSort%2 === 0) items.sort((a,b) => b.damaged - a.damaged);
        else items.sort((a,b) => a.damaged - b.damaged);
    }
  };

  {items && items.forEach((item,index) => {
    item.num = index + 1    
})}

  const columns = [
    {
      name: (
        <h3 className="headings">
          Serial no
        </h3>
      ),
      selector: (row,index) => (
        <p>{row.num}</p>
    ),
      wrap: true,
    },
    {
      name: (
        <h3 className="headings" onClick={() => sortFunction("name")}>
          Name
        </h3>
      ),
      selector: (row) => (
        <a className="cellLink" href={`/item/${row._id}`}>
          <p>{row.name}</p>
        </a>
      ),
      sortable: true,
      wrap: true,
    },
    {
      name: (
        <h3 className="headings" onClick={() => sortFunction("cost")}>
          Cost
        </h3>
      ),
      selector: (row) => <span className="cellData">{row.cost}</span>,
      sortable: true,
      wrap: true,
    },
    {
      name: (
        <h4 className="headings" onClick={() => sortFunction("stock")}>
          Stock
        </h4>
      ),
      selector: (row) => <span className="cellData">{row.stock}</span>,
      sortable: true,
      wrap: true,
    },
    {
        name: (
          <h4 className="headings" onClick={() => sortFunction("damaged")}>
            Damaged
          </h4>
        ),
        selector: (row) => <span className="cellData redClass">{row.damaged}</span>,
        sortable: true,
        wrap: true,
    },
    {
        name: "",
        cell: (row) => (
          <button
            onClick={() => {
              makeId(row._id);
              openDialogAdd();
            }}
            className="addBtn"
          >
            Add
          </button>
        ),
    },
    {
        name: "",
        cell: (row) => (
          <button
            onClick={() => {
              makeId(row._id);
              openDialogRem();
            }}
            className="remBtn"
          >
            Remove
          </button>
        ),
    },
    {
      name: "",
      cell: (row) => (
        <a className="rlinks" href={`/update/item/${row._id}`}>
          <button className="btn">Edit</button>
        </a>
      ),
    },
    {
      name: "",
      cell: (row) => (
        <button
          onClick={() => {
            makeId(row._id);
            openDialogDel();
          }}
          className="delBtn"
        >
          Delete
        </button>
      ),
    },
  ]; 

  
  const searchSubmit = (e) => {
    e.preventDefault();
    dispatch(getAllItems("MESS", value));
  };


  useEffect(() => {
    if (error) {
      failure(error);
      dispatch(clearErrors());
    }

    if (deleteError) {
      failure(deleteError);
      dispatch(clearErrors());
    }

    if (isDeleted === true) {
      successDelete();
      dispatch({ type: DELETE_ITEM_RESET });
    }

    if(isAdded){
        successAdd();
        dispatch({type: ADD_STOCK_RESET});
    }

    if(isRemoved){
        successRemove();
        dispatch({type: REMOVE_STOCK_RESET});
    }

    dispatch(getAllItems("MESS"));
  }, [dispatch, error, deleteError, isDeleted,isAdded,isRemoved]);


  return (
    <Fragment>
      {(isOpen===1) && (
        <ReactDialogBox
          closeBox={closeDialog}
          modalWidth="28%"
          headerBackgroundColor="rgb(192, 188, 188)"
          headerTextColor="rgb(39, 39, 39)"
          headerHeight="50"
          closeButtonColor="#D92736"
          bodyBackgroundColor="white"
          bodyTextColor="black"
          bodyHeight="35%"
          headerText="Enter Admin Password"
        >
          <div>
            <form className="loginForm" onSubmit={deleteSubmit}>
              <div className="password">
                <input
                  type="password"
                  placeholder="password"
                  value={password}
                  onChange={(e) => setPassword(e.target.value)}
                ></input>
              </div>
              <button type="submit" className="loginBtn">
                delete
              </button>
            </form>
          </div>
        </ReactDialogBox>
      )}
      {(isOpen===2) && (
        <ReactDialogBox
          closeBox={closeDialog}
          modalWidth="28%"
          headerBackgroundColor="rgb(192, 188, 188)"
          headerTextColor="rgb(39, 39, 39)"
          headerHeight="50"
          closeButtonColor="#D92736"
          bodyBackgroundColor="white"
          bodyTextColor="black"
          bodyHeight="35%"
          headerText="Enter Details"
        >
          <div>
            <form className="loginForm" onSubmit={addSubmit}>
              <div className="password">
              <input
                  type="number"
                  placeholder="cost"
                  value={costd}
                  onChange={(e) => setCostd(e.target.value)}
                ></input>
                <input
                  type="number"
                  placeholder="stock"
                  value={stockd}
                  onChange={(e) => setStockd(e.target.value)}
                ></input>
                <input
                  type="text"
                  placeholder="asked by"
                  value={askedBy}
                  onChange={(e) => setAskedBy(e.target.value)}
                ></input>
                <input
                  type="password"
                  placeholder="password"
                  value={password}
                  onChange={(e) => setPassword(e.target.value)}
                ></input>
              </div>
              <button type="submit" className="loginBtn">
                Add
              </button>
            </form>
          </div>
        </ReactDialogBox>
      )}
      {(isOpen===3) && (
        <ReactDialogBox
          closeBox={closeDialog}
          modalWidth="28%"
          headerBackgroundColor="rgb(192, 188, 188)"
          headerTextColor="rgb(39, 39, 39)"
          headerHeight="50"
          closeButtonColor="#D92736"
          bodyBackgroundColor="white"
          bodyTextColor="black"
          bodyHeight="35%"
          headerText="Enter Admin Password"
        >
          <div>
            <form className="loginForm" onSubmit={removeSubmit}>
              <div className="password">
                <input
                  type="number"
                  placeholder="stock"
                  value={stockd}
                  onChange={(e) => setStockd(e.target.value)}
                ></input>
                <input
                  type="text"
                  placeholder="asked by"
                  value={askedBy}
                  onChange={(e) => setAskedBy(e.target.value)}
                ></input>
                <input
                  type="password"
                  placeholder="password"
                  value={password}
                  onChange={(e) => setPassword(e.target.value)}
                ></input>
              </div>
              <button type="submit" className="loginBtn">
                Remove
              </button>
            </form>
          </div>
        </ReactDialogBox>
      )}
      <InventorySidebar/>
      <div className="dashboard">
        <div className="productListContainer">
          <h1 id="productListHeading">Modern College Mess items</h1>
          <div className="searchBox">
            <p className="searchName">Name</p>
            <div>
              <form className="searchForm" onSubmit={searchSubmit}>
                <input
                  type="text"
                  className="searchInput"
                  value={value}
                  onChange={(e) => {
                    setValue(e.target.value);
                  }}
                />
                <button type="submit" className="searchBtn">
                  Search
                </button>
              </form>
            </div>
          </div>
          <DataTable
            pagination
            columns={columns}
            data={items}
            className="productListTable"
            highlightOnHover={true}
            persistTableHead={true}
            fixedHeader={true}
          />
        </div>
      </div>
    </Fragment>
  );
};

export default Mess;
